
// team
.team {
    padding-top: 60px;
    border-radius: 30px;
    background: var(--background-on-black-light-black);
    overflow: hidden;
    margin-left: 35px;
    margin-right: 35px;

    &__title {
        padding-bottom: 80px;
        border-bottom: 4px dotted var(--background-on-black-gray);

        & span {
            position: relative;
            padding-right: 152px;
            &::after {
                content: '';
                display: inline-block;
                width: 40px;
                height: 49px;
                background-image: url("data:image/svg+xml,%3Csvg width='40' height='49' viewBox='0 0 40 49' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M10.2084 1.47155C10.6138 0.576357 11.5173 0 12.5184 0H32.4824C34.3594 0 35.5832 1.93754 34.7423 3.58568L29.0637 14.7155H37.4677C39.7202 14.7155 40.8464 17.3839 39.2547 18.9438L9.33996 48.2595C7.45293 50.1087 4.30954 48.2055 5.15546 45.726L11.5323 26.9784H2.52763C2.10653 26.9784 1.69212 26.8753 1.32202 26.6785C0.951918 26.4816 0.637861 26.1973 0.408371 25.8513C0.178881 25.5053 0.0412297 25.1086 0.00792093 24.6972C-0.0253878 24.2859 0.0467011 23.8729 0.217639 23.4957L10.2084 1.47155Z' fill='%23DE523E'/%3E%3C/svg%3E");
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
                position: absolute;
                top: 50%;
                right: 56px;
                transform: translate(0%, -50%);
            }
        }
    }

    & .container {
        padding: 0;
    }

    &__row {
        display: flex;
        justify-content: space-between;
    }

    &__photo {
        flex: 1 0 40%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 16px;

        &-pic {
            display: block;
            max-width: 480px;

            & img {
                width: 100%;
                height: 460px;
                object-fit: cover;
                border-radius: 20px;
            }
        }
    }

    &__wrap {
        flex: 1 0 30%;
        position: relative;
        &:first-child {
            border-right: 4px dotted var(--background-on-black-gray);
        }
        &:last-child {
            border-left: 4px dotted var(--background-on-black-gray);
            & .team__wrap-title {
                &::after {
                    left: 0;
                }
            }
        }
        &::after {
            content: '';
            display: block;
            width: 100%;
            height: 100px;
            background: linear-gradient(180deg, rgba(44, 46, 55, 0) 0.61%, rgba(44, 46, 55, 0.51) 53.99%, #2c2e37 74.28%);
            position: absolute;
            bottom: -20px;
            left: 0;
            pointer-events: none;
        }

        &-title {
            padding: 40px;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 40px;
            line-height: 110%;
            text-transform: uppercase;
            color: var(--text-tertiary-white);
            position: relative;
            &::after {
                content: '';
                display: block;
                width: 50vw;
                height: 1px;
                border-bottom: 4px dotted var(--background-on-black-gray);
                position: absolute;
                right: 0;
                bottom: 0;
            }
        }

        &-inner {
            padding: 40px 6px 0 38px;
        }
    }

    &__tab {
        &-top {
            display: none;
        }
        &-btns {
            padding-bottom: 50px;
            padding-right: 30px;
            display: flex;
            flex-direction: column;
            gap: 28px;
            max-height: 600px;
            overflow-y: auto;

            &::-webkit-scrollbar {
                width: 4px;
            }
            &::-webkit-scrollbar-thumb {
                background: var(--background-base-orange);
                border-radius: 20px;
            }
            &::-webkit-scrollbar-track {
                background: transparent;
            }
        }
        &-btn {
            display: flex;
            align-items: center;
            gap: 12px;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 40px;
            line-height: 110%;
            text-transform: uppercase;
            text-align: left;
            color: var(--text-primary-white);
            transition: all 0.3s ease;
            &:hover {
                color: var(--text-orange);
            }

            &.active {
                color: var(--text-orange);
                &::before {
                    content: '';
                    display: block;
                    width: 22px;
                    height: 22px;
                    background: url('./../img/icons/star.svg') no-repeat center / contain;
                    flex-shrink: 0;
                }
            }
        }
    }


    &__list {
        padding-bottom: 50px;
        padding-right: 20px;
        display: flex;
        flex-direction: column;
        gap: 4px;
        list-style-type: none;

        max-height: 600px;
        overflow-y: auto;

        &::-webkit-scrollbar {
            width: 4px;
        }
        &::-webkit-scrollbar-thumb {
            background: var(--background-base-orange);
            border-radius: 20px;
        }
        &::-webkit-scrollbar-track {
            background: transparent;
        }
    }

    &__item {
        border: 1px solid var(--stroke-light-black);
        border-radius: 10px;
        padding: 24px;

        &-fio {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 28px;
            line-height: 114%;
            text-transform: uppercase;
            color: var(--text-primary-white);
            margin-bottom: 12px;
        }

        &-post {
            font-size: 14px;
            line-height: 143%;
            color: var(--text-secondary-white);
        }
    }
}

@media (max-width: 1300px) {
    .team {
        margin-left: 10px;
        margin-right: 10px;
    
        &__title {
            & span {
                padding-right: 105px;
                &::after {
                    width: 35px;
                    height: 49px;
                    right: 35px;
                }
            }
        }
    
        &__wrap {
            &-title {
                padding: 32px 24px;
                font-size: 32px;
            }
    
            &-inner {
                padding: 32px 6px 0 24px;
            }
        }
    
        &__tab {
            &-btns {
                padding-bottom: 50px;
                padding-right: 10px;
                gap: 28px;
            }
            &-btn {
                font-size: 32px;
                &.active {
                    &::before {
                        width: 20px;
                        height: 20px;
                    }
                }
            }
        }
    
    
        &__list {
            padding-right: 10px;
        }
    
        &__item {
            padding: 24px;
            &-fio {
                font-size: 24px;
                margin-bottom: 6px;
            }
        }
    }
    
}

@media (max-width: 1024px) {
    .team {
        padding-top: 40px;
        &__title {
            padding-bottom: 42px;
        }
        & .container {
            padding: 15px;
        }
    
        &__row {
            flex-direction: column;
            justify-content: space-between;
        }
    
        &__photo {
            padding: 32px 0;
            position: relative;
            &-pic {
                display: block;
                max-width: 100%;
            }
            &::after, &::before {
                content: '';
                display: block;
                width: 100vw;
                height: 1px;
                border-bottom: 4px dotted var(--background-on-black-gray);
                position: absolute;
                left: -20px;
                bottom: 0;
            }
            &::after {
                bottom: 0;
            }
            &::before {
                top: 0;
            }
        }
    
        &__wrap {
            padding: 24px 0;
            &:first-child {
                border: none;
            }
            &:last-child {
                border: none;
            }
            &::after {
                content: none;
            }
    
            &-title {
                padding: 0;
                font-size: 24px;
                margin-bottom: 12px;
                &::after {
                    content: none;
                }
            }
    
            &-inner {
                padding: 0px;
            }
        }
    
        &__tab {
            &-mobile {
                &.active {
                    & .team__tab-icon {
                        transform: rotate(180deg);
                        box-shadow: 0 0 10px 0 rgba(255, 255, 255, 0.4);
                    }
                    & .team__tab-btns {
                        max-height: 250px;
                        padding: 8px 0;
                    }
                }
            }
            &-top {
                border-radius: 10px;
                padding: 24px;
                background: var(--background-on-black-gray);
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 4px;
                font-family: var(--font-family);
                font-weight: 500;
                font-size: 24px;
                line-height: 1.08333;
                text-transform: uppercase;
                color: var(--text-primary-white);
            }
            &-icon {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 20px;
                height: 20px;
                border-radius: 20px;
                background: var(--background-base-white);
                transition: all 0.3s ease;
            }
            &-btns {
                padding: 0;
                padding-right: 10px;
                border-radius: 10px;
                background: var(--background-on-black-gray);
                display: flex;
                flex-direction: column;
                gap: 0px;
                max-height: 0px;
                transition: all 0.3s ease;
            }
            &-btn {
                padding: 12px 24px;
                font-size: 24px;
                &.active {
                    &::before {
                        width: 14px;
                        height: 14px;
                    }
                }
            }
        }
    
    
        &__list {
            padding-bottom: 50px;
            padding-right: 10px;
            max-height: 400px;

            &-wrap {
                position: relative;
        
                &::after {
                    content: '';
                    display: block;
                    width: 100%;
                    height: 100px;
                    background: linear-gradient(180deg, rgba(44, 46, 55, 0) 0.61%, rgba(44, 46, 55, 0.51) 53.99%, #2c2e37 74.28%);
                    position: absolute;
                    bottom: -20px;
                    left: 0;
                    pointer-events: none;
                }
            }
        }
    
        &__item {
            padding: 20px;
            &-fio {
                font-size: 24px;
                margin-bottom: 6px;
            }
            &-post {
                font-size: 12px;
            }
        }
    }
}

@media (max-width: 768px) {
    .team {
        border-radius: 20px;
        margin-left: 0;
        margin-right: 0;
    
        &__title {
            padding-bottom: 34px;
            & span {
                padding-right: 48px;
                &::after {
                    width: 16px;
                    height: 20px;
                    right: 16px;
                }
            }
        }
        &__photo {
            &-pic {
                & img {
                    height: 360px;
                    border-radius: 10px;
                }
            }
        }
    }
}
@media (max-width: 500px) {
    .team {
        &__photo {
            &-pic {
                & img {
                    height: 200px;
                }
            }
        }
    }
}