
// steps
.steps {
    border-radius: 30px;
    background: var(--background-on-black-light-black);
    padding: 65px 0 140px;
    margin-right: 15px;
    margin-left: 15px;
    overflow: hidden;

    &__title {
        text-align: center;
        max-width: 1430px;
        margin: 0 auto 120px;
        & span {
            color: var(--text-orange);
        }
    }

    &__list {
        list-style-type: none;
        counter-reset: steps;
    }

    &__item {
        position: relative;
        padding: 0 15px;
        &::after {
            content: '';
            display: block;
            width: calc(100% + 40px);
            height: 1px;
            border-bottom: 4px dotted var(--background-on-black-gray);
            position: absolute;
            bottom: 0;
            left: -20px;
        }

        &:first-child {
            &::before {
                content: '';
                display: block;
                width: calc(100% + 40px);
                height: 1px;
                border-bottom: 4px dotted var(--background-on-black-gray);
                position: absolute;
                top: 0;
                left: -20px;
            }
        }

        
        &-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 30px;
            padding: 60px 0;
            &::before {
                content: counter(steps, decimal-leading-zero);
                counter-increment: steps;
                font-family: var(--font-family);
                font-weight: 500;
                font-size: 88px;
                line-height: 105%;
                text-transform: uppercase;
                color: var(--background-on-black-gray);
            }

            &.accordion--active {
                & .steps__item-name {
                    color: var(--text-orange);
                }
                & .steps__item-btn svg {
                    transform: rotate(-90deg);
                }
            }
        }

        &-name {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 88px;
            line-height: 105%;
            text-transform: uppercase;
            text-align: center;
            color: var(--text-primary-white);
            flex-grow: 1;
            transition: all 0.3s ease;
        }

        &-btn {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-shrink: 0;
            width: 64px;
            height: 64px;
            border-radius: 10px;
            background: var(--background-on-black-gray);
            & svg {
                transition: all 0.3s ease;
            }
        }

        &-text {
            text-align: center;
            color: var(--text-primary-white);
            max-width: 900px;
            margin: 0 auto 60px;
            & p:not(:last-child) {
                margin-bottom: 24px;
            }
        }
    }
}

@media (max-width: 1300px) {
    .steps {
        &__title {
            margin: 0 auto 60px;
        }

        &__item {
            &-top {
                padding: 40px 0;
                &::before {
                    font-size: 66px;
                }
            }

            &-name {
                font-size: 66px;
            }

            &-btn {
                width: 58px;
                height: 58px;
            }

            &-text {
                margin: 0 auto 60px;
            }
        }
    }
}

@media (max-width: 768px) {
    .steps {
        border-radius: 20px;
        padding: 40px 0 70px;
        margin-right: 0;
        margin-left: 0;

        &__title {
            margin-bottom: 32px;
        }

        &__item {
            padding: 0;

            &-top {
                gap: 12px;
                padding: 24px 0;
                &::before {
                    font-size: 32px;
                }
            }

            &-name {
                font-size: 32px;
            }

            &-btn {
                width: 28px;
                height: 28px;
                border-radius: 8px;
                & svg {
                    max-width: 6px;
                }
            }

            &-text {
                margin: 0 auto 28px;
                & p:not(:last-child) {
                    margin-bottom: 12px;
                }
            }
        }
    }
}