
// form
.form {
    &__row {
        display: flex;
        gap: 10px;
    }

    &__content {
        border-radius: 30px;
        width: 100%;
        max-width: calc(50% - 5px);
        padding: 60px 35px 60px 60px;
        background: var(--background-on-black-light-black);

        transform: translate(-50%, 0%);
        transition: all 0.6s ease;

        &.animation-active {
            transform: none;
        }
    }

    &__title {
        margin-bottom: 32px;
    }

    &__subtitle {
        line-height: 1.3;
        color: var(--text-secondary-white);
        margin-bottom: 40px;
    }

    &__wrap {
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    &__wrap-bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        margin-top: 12px;
    }

    &__btn {
        order: 3;
    }

    &__picture {
        border-radius: 30px;
        width: 100%;
        max-width: calc(50% - 5px);
        background: var(--background-on-black-light-black) url('./../img/form/form-bg.webp') no-repeat center / cover;
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;

        transform: translate(50%, 0%);
        transition: all 0.6s ease;
        z-index: 2;

        &.animation-active {
            transform: none;
        }

        &-pic {
            max-width: 70%;
        }
    }
}

@media (max-width: 1500px) {
    .form {
        &__content {
            padding: 40px;
        }
        &__wrap-bottom {
            flex-direction: column;
            align-items: stretch;
            gap: 12px;
            margin-top: 12px;
        }
    }
}

@media (max-width: 1024px) {
    .form {
        &__content {
            max-width: 100%;
            transform: none;
        }

        &__picture {
            display: none;
        }
    }
}

@media (max-width: 768px) {
    .form {
        &__content {
            border-radius: 20px;
            padding: 40px 10px;
        }
    
        &__title {
            margin-bottom: 24px;
        }
    
        &__subtitle {
            margin-bottom: 28px;
        }
    
        &__wrap {
            gap: 8px;
        }
    
        &__wrap-bottom {
            margin-top: 16px;
        }
    }
}