
// banner
.banner {
    padding: 50px 0;
    height: calc(100vh - 110px);
    position: relative;
    
    &.animation-active .banner__bg {
        opacity: 1;
        transform: translateY(0);
    }
    
    & .container {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        height: 100%;
    }

    &__inner {
        position: relative;
    }

    &__title {
        text-align: center;
        margin-bottom: 52px;
        & span {
            color: var(--text-orange);
        }
    }

    &__subtitle {
        text-align: center;
        max-width: 700px;
        margin: 0 auto;
        color: var(--text-primary-white);
    }

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 24px;
        &-text {
            font-size: 18px;
            line-height: 1.22222;
            color: var(--base-orange-0);
            padding-right: 24px;
            max-width: 324px;
            border-right: 1px solid var(--stroke-light-black);
        }
    }

    &__bg {
        position: absolute;
        top: -112px;
        left: 0;
        width: 100%;
        height: calc(100% + 112px);
        z-index: -1;
        opacity: 0;
        transform: translateY(-50%);
        transition: all 0.9s ease;

        & img {
            object-position: bottom;
            width: 100%;
        }
    }

    &__sticker {
        position: absolute;
        
        &-about-1 {
            left: 0;
            bottom: 0;
        }
        &-about-2 {
            right: 0;
            bottom: 0;
        }

        &-vacances-1 {
            left: 0;
            top: -90px;
        }
        &-vacances-2 {
            right: 0;
            bottom: 45px;
        }
        &-service-1 {
            &:nth-of-type(1) {left: -5%;top: -17%; max-width: 550px;}
            &:nth-of-type(2) {right: 5%;bottom: 30%;}
        }
    }
}

.banner-service {
    position: relative;
    &::after, &::before {
        content: '';
        display: block;
        width: 180%;
        height: 1160px;
        aspect-ratio: 2 / 1;
        border-radius: 50%;
        box-shadow: 0px 0px 171px 136px rgba(222, 82, 62, 0.8) inset, 0px 0px 270px 156px rgba(222, 82, 62, 0.8);
        position: absolute;
        top: auto;
        top: 0;
        transform: translate(0%, -100%);
        opacity: 0;
        z-index: -1;
        filter: blur(70px);
        transition: transform 0.9s ease, opacity 0.8s ease;
    }

    &::before {
        left: 60%;
    }
    &::after {
        right: 60%;
    }

    &.animation-active {
        &::after, &::before {
            transform: translate(0%, -60%);
            opacity: 1;
        }
    }
}

.banner-vacances {
    position: relative;
    &::after, &::before {
        content: '';
        display: block;
        width: 140%;
        aspect-ratio: 5 / 1;
        border-radius: 50%;
        box-shadow: 0px 0px 271px 176px rgba(222, 82, 62, 0.8) inset, 0px 0px 190px 126px rgba(222, 82, 62, 0.8);
        position: absolute;
        top: 50%;
        transform: translate(0%, -100%);
        opacity: 0;
        z-index: -1;
        filter: blur(70px);
        transition: transform 0.9s ease, opacity 0.8s ease;
    }

    &::before {
        left: 70%;
    }
    &::after {
        right: 70%;
    }

    &.animation-active {
        &::after, &::before {
            transform: translate(0%, -50%);
            opacity: 1;
        }
    }
}

.banner-about {
    position: relative;
    &::after {
        content: '';
        display: block;
        width: 180%;
        height: 1160px;
        height: auto;
        aspect-ratio: 1 / 0.6;
        border-radius: 50%;
        box-shadow: 0px 0px 171px 136px rgba(222, 82, 62, 0.8) inset, 0px 0px 270px 156px rgba(222, 82, 62, 0.8);
        position: absolute;
        bottom: 25%;
        left: 50%;
        transform: translate(-50%, -20%);
        opacity: 0;
        z-index: -1;
        filter: blur(70px);
        transition: transform 0.7s ease, opacity 0.8s ease;
    }

    &.animation-active {
        &::after, &::before {
            transform: translate(-50%, 0%);
            opacity: 1;
        }
    }
}

@media (max-width: 1300px) {
    .banner {
        &__sticker {
            &-about-1 {
                left: -50px;
                bottom: -60px;
                max-width: 30%;
            }
            &-about-2 {
                right: -50px;
                bottom: -60px;
                max-width: 30%;
            }

            &-vacances-1 {
                left: -20px;
                top: -90px;
                max-width: 250px;
            }
            &-vacances-2 {
                right: -30px;
                bottom: 47px;
                max-width: 220px;
            }

            &-service-1 {
                &:nth-of-type(1) {left: -6%;top: -10%; max-width: 420px;}
                &:nth-of-type(2) {right: 2%;bottom: 33%;max-width: 220px;}
            }
        }
    }
}
@media (max-width: 1024px) {
    .banner {
        height: calc(100vh - 88px);
        &__sticker {
            display: none;
        }
        &__bg {
            left: -50%;
            width: 200%;
            & img {
                height: 150%;
            }
        }
    }

    .banner-service {
        position: relative;
        &::after, &::before {
            width: 180%;
            height: 60vh;
            box-shadow: 0px 0px 101px 86px rgba(222, 82, 62, 0.8) inset, 0px 0px 110px 86px rgba(222, 82, 62, 0.8);
            filter: blur(40px);
        }
    }

    .banner-about {
        &::after {
            min-width: 1400px;
            width: 200%;
            height: auto;
            aspect-ratio: 1 / 0.8;
            box-shadow: 0px 0px 260px 110px rgba(222, 82, 62, 0.8) inset, 0px 0px 120px 70px rgba(222, 82, 62, 0.8);
        }
    }
}
@media (max-width: 768px) {
    .banner {
        height: calc(100vh - 84px);

        &__wrap {
            width: 100%;
        }
        &__bottom {
            flex-direction: column;
            justify-content: space-between;
            width: 100%;
            gap: 110px;
            &-text {
                font-size: 14px;
                padding-right: 0;
                max-width: 300px;
                text-align: center;
                border-right: none;
            }
            & button {
                width: 100%;
            }
        }
    }

    .banner-service {
        &::after, &::before {
            width: 180%;
            height: 60vh;
            box-shadow: 0px 0px 50px 36px rgba(222, 82, 62, 0.8) inset, 0px 0px 140px 56px rgba(222, 82, 62, 0.8);
            filter: blur(20px);
        }
        &::after {
            right: 70%;
        }
        &::before {
            left: 70%;
        }
        &.animation-active {
            &::after, &::before {
                transform: translate(0%, -50%);
            }
        }
    }

    .banner-vacances {
        &::after, &::before {
            min-width: 1000px;
            max-height: 364px;
            box-shadow: 0px 0px 151px 126px rgba(222, 82, 62, 0.8) inset, 0px 0px 290px 156px rgba(222, 82, 62, 0.8);
            filter: blur(50px);
        }
        &::before {
            left: 90%;
        }
        &::after {
            right: 90%;
        }
    }

    .banner-about {
        position: relative;
        &::after {
            min-width: 1400px;
            width: 200%;
            height: auto;
            aspect-ratio: 1 / 0.8;
            box-shadow: 0px 0px 300px 120px rgba(222, 82, 62, 0.8) inset, 0px 0px 100px 60px rgba(222, 82, 62, 0.8);
        }
    }
}