
// contacts
.contacts {
    border-top: 4px dotted var(--background-on-black-light-black);
    border-bottom: 4px dotted var(--background-on-black-light-black);
    position: relative;
    padding: 60px 0;
    overflow: hidden;

    &__line {
        display: block;
        width: 4px;
        height: 100%;
        transform: translate(-50%, 0);
        border-right: 4px dotted var(--background-on-black-light-black);
        position: absolute;
        top: 0;
        left: 50%;
    }

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 120px;
    }

    &__content {
        flex: 1 0 40%;
    }

    &__items {
        display: flex;
        flex-wrap: wrap;
        gap: 12px;
        margin-bottom: 40px;
    }

    &__item {
        flex: 1 0 40%;
        max-width: calc(50% - 6px);
        border-radius: 20px;
        padding: 40px;
        background: var(--background-on-black-light-black);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        min-height: 353px;
        position: relative;
        overflow: hidden;
    }

    &__item-dots {
        display: flex;
        gap: 4px;

        & span {
            display: block;
            border-radius: 100%;
            background: var(--background-base-orange);
            width: 10px;
            height: 10px;
        }
    }

    &__item-icon {
        position: absolute;
        top: 25px;
        right: 30px;
    }

    &__item-link, &__item-text {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 40px;
        line-height: 1.1;
        text-transform: uppercase;
        color: var(--text-primary-white);
    }

    &__item-link {
        word-wrap: break-word;
        text-decoration: underline !important;
        transition: all 0.3s ease;
        &:hover {
            color: var(--state-orange);
        }
    }

    &__networks {
        display: flex;
        flex-direction: column;
        gap: 16px;
    }

    &__maps {
        flex: 1 0 40%;
        border-radius: 20px;
        width: 100%;
        height: 100%;
        min-height: 860px;
        position: relative;
        overflow: hidden;
    }

    &__map {
        position: absolute;
        inset: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
    }
}

[class*="ymaps-2"][class*="-ground-pane"] {
    -webkit-filter: grayscale(90%) invert(90%);
            filter: grayscale(90%) invert(90%);
}


@media (max-width: 1300px) {
    .contacts {    
        &__line {
            width: calc(100% + 40px);
            height: 4px;
            transform: translate(-20px, 0);
            border-right: none;
            border-bottom: 4px dotted var(--background-on-black-light-black);
            position: static;
        }
    
        &__row {
            flex-direction: column;
            gap: 60px;
        }

        &__maps {
            min-height: 660px;
        }
    }
}

@media (max-width: 768px) {
    .contacts {
        padding: 32px 0;

        &__row {
            gap: 32px;
        }
    
        &__items {
            flex-direction: column;
            gap: 8px;
            margin-bottom: 24px;
        }
    
        &__item {
            max-width: 100%;
            border-radius: 10px;
            padding: 24px 24px 27px 24px;
            gap: 60px;
            min-height: auto;
        }
    
        &__item-dots {
            & span {
                width: 6px;
                height: 6px;
            }
        }
    
        &__item-icon {
            display: block;
            top: 20px;
            right: 24px;
            & img {
                width: 100%;
                height: 100%;
                max-height: 60px;
                object-fit: contain;
            }
        }
    
        &__item-link, &__item-text {
            font-size: 28px;
        }

        &__btn {
            width: 100%;
        }
    
        &__maps {
            border-radius: 10px;
            min-height: 520px;
        }
    }
}