
// price
.price {
    &__title {
        max-width: 1315px;
    } 

    &__row {
        display: flex;
        gap: 10px;
    }

    &__card {
        flex: 1 0 40%;
        border-radius: 30px;
        padding: 52px;
        overflow: hidden;
    }

    &__left {
        background: var(--background-base-orange);
        color: var(--text-primary-white);

        transform: translate(50%, 0%);
        transition: all 0.6s ease;
        z-index: 2;

        &.animation-active {
            transform: none;
        }

        &-top {
            position: relative;
            padding-bottom: 32px;
            margin-bottom: 32px;
            font-size: 14px;
            line-height: 143%;
            color: var(--text-secondary-orange);

            &::after {
                content: '';
                display: block;
                width: calc(100% + 110px);
                height: 1px;
                border-bottom: 4px dotted var(--background-base-light-orange);
                position: absolute;
                bottom: 0;
                left: -52px;
            }
        }

        &-sticker {
            position: absolute;
            bottom: 0;
            right: 0;
            z-index: 2;
            transform: translate(0, 50%);
            max-width: 187px;
        }

        &-cost {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 88px;
            line-height: 105%;
            text-transform: uppercase;
            color: var(--text-primary-white);
            margin-bottom: 12px;
        }

        &-deadline {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 40px;
            line-height: 110%;
            text-transform: uppercase;
            color: var(--text-primary-white);
            margin-bottom: 60px;
        }

        &-text {
            position: relative;
            font-size: 18px;
            line-height: 122%;
            color: var(--text-secondary-orange);
            padding-top: 37px;
            padding-bottom: 40px;
            margin-bottom: 50px;

            &::before {
                content: '';
                display: block;
                width: 88px;
                height: 66px;
                background: url('./../img/icons/quote.svg') no-repeat center / contain;
                position: absolute;
                top: 0;
                left: 0;
            }

            &::after {
                content: '';
                display: block;
                width: calc(100% + 110px);
                height: 1px;
                border-bottom: 4px dotted var(--background-base-light-orange);
                position: absolute;
                bottom: 0;
                left: -52px;
            }
        }
    }

    &__right {
        background: var(--background-on-black-light-black);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
        padding-bottom: 12px;

        transform: translate(-50%, 0%);
        transition: all 0.6s ease;

        &.animation-active {
            transform: none;
        }

        &::after {
            content: '';
            display: block;
            width: 1px;
            height: calc(100% - 250px);
            border-right: 4px dotted #494c5b;
            position: absolute;
            bottom: 0;
            left: 50%;
        }

        &::before {
            content: '';
            display: block;
            width: 1162px;
            height: 1162px;
            border-radius: 50%;
            box-shadow: 0px -10px 151px 76px rgba(222, 82, 62, 0.99) inset, 0px -11px 90px 66px rgba(222, 82, 62, 0.3);
            position: absolute;
            top: -911px;
            left: 50%;
            transform: translate(-50%, 0%);
            z-index: 2;
        }

        &-title {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 16px;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 56px;
            line-height: 114%;
            text-transform: uppercase;
            color: var(--text-primary-white);
            z-index: 3;
            margin-bottom: 200px;
        }

        &-list {
            display: flex;
            flex-wrap: wrap;
            list-style-type: none;
            counter-reset: item;
        }

        &-item {
            flex: 1 0 40%;
            padding: 40px 0;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 28px;
            line-height: 114%;
            text-transform: uppercase;
            color: var(--text-primary-white);
            display: flex;
            align-items: center;
            gap: 32px;
            position: relative;

            &:nth-child(2n-1) {
                padding-right: 32px;
            }
            &:nth-child(2n) {
                padding-left: 32px;
            }

            &:nth-child(1) {
                &::after {
                    content: '';
                    display: block;
                    width: 250%;
                    height: 1px;
                    border-bottom: 4px dotted #494c5b;
                    position: absolute;
                    bottom: 0;
                    left: -52px;
                }
            }

            &::before {
                content: counter(item, decimal-leading-zero);
                counter-increment: item;
                display: block;
                font-family: var(--font-family);
                font-weight: 500;
                font-size: 66px;
                line-height: 80%;
                color: var(--background-base-white);
                opacity: 0.2;
            }
        }
    }
}


@media (max-width: 1300px) {
    .price {
        &__row {
            flex-direction: column;
            gap: 10px;
        }
        &__left {
            transform: none;
        }
        &__right {
            transform: translate(0%, -100%);
            &.animation-active {
                transform: none;
            }
        }
    }
}

@media (max-width: 768px) {
    .price {
        &__row {
            gap: 8px;
        }

        &__card {
            border-radius: 20px;
            padding: 40px 10px;
        }

        &__left {
            &-top {
                padding-bottom: 24px;
                margin-bottom: 24px;
                font-size: 12px;
            }

            &-sticker {
                display: none;
            }

            &-cost {
                font-size: 44px;
                margin-bottom: 12px;
            }

            &-deadline {
                font-size: 28px;
                margin-bottom: 50px;
            }

            &-text {
                font-size: 14px;
                padding-top: 28px;
                padding-bottom: 24px;
                margin-bottom: 24px;

                &::before {
                    width: 50px;
                    height: 38px;
                }
            }

            & button {
                width: 100%;
            }
        }

        &__right {
            &::after {
                content: none;
            }

            &::before {
                content: '';
                display: block;
                width: 180%;
                height: auto;
                aspect-ratio: 1 / 1;
                border-radius: 50%;
                box-shadow: 0px -10px 71px 36px rgba(222, 82, 62, 0.99) inset, 0px -11px 90px 66px rgba(222, 82, 62, 0.3);
                position: absolute;
                top: auto;
                bottom: 76%;
                left: 50%;
                transform: translate(-50%, 0%);
                z-index: 2;
            }

            &-title {
                gap: 12px;
                font-size: 32px;
                margin-bottom: 100px;
                & svg {
                    max-width: 15px;
                }
            }

            &-list {
                flex-direction: column;
            }

            &-item {
                padding: 24px 0;
                font-size: 24px;
                gap: 24px;
                position: relative;

                &:nth-child(2n-1) {
                    padding-right: 0;
                }
                &:nth-child(2n) {
                    padding-left: 0;
                }

                &::after {
                    content: '';
                    display: block;
                    width: 250%;
                    height: 1px;
                    border-bottom: 4px dotted #494c5b;
                    position: absolute;
                    bottom: 0;
                    left: -52px;
                }

                &:last-child {
                    padding-bottom: 0;
                    &::after {
                        content: none;
                    }
                }

                &::before {
                    font-size: 55px;
                }
            }
        }
    }
}