
// clients
.clients {

    &__title {
        margin-bottom: 60px;
    }

    & .marquee__tracks, & .marquee__track {
        gap: 20px;
    }

    &__lines {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &__line {
        display: flex;
        gap: 20px;
    }

    &__item {
        border: 1px solid var(--stroke-light-black);
        border-radius: 20px;
        width: 425px;
        height: 200px;
        display: flex;
        align-items: center;
        justify-content: center;

        &-pic {
            max-width: 60%;
        }
    }
}

@media (max-width: 768px) {
    .clients {
        &__title {
            margin-bottom: 32px;
        }
    
        & .marquee__tracks, & .marquee__track {
            gap: 8px;
        }
    
        &__lines {
            gap: 10px;
        }
    
        &__line {
            gap: 8px;
        }
    
        &__item {
            border-radius: 10px;
            width: 180px;
            height: 100px;
        }
    }
}