
// history
.history {
    // min-height: 100vh;

    // & .container {
    //     min-height: 100vh;
    // }

    & .counter-blocks {
        & span {
            &:first-child {
                border-color: var(--stroke-ligh-light-black);
                background: var(--background-on-black-gray);
            }
        }
    }

    &__title {
        margin: 0 auto 80px;
        max-width: 945px;
        text-align: center;

        & span {
            color: var(--text-orange);
        }
    }

    &__row {
        display: flex;
        gap: 10px;
        //min-height: 100%;
    }

    &__card {
        border-radius: 30px;
        width: 100%;
        max-width: calc(50% - 5px);
        overflow: hidden;
        min-height: 900px;
    }

    &__photo {
        &-pic {
            display: block;
            width: 100%;
            height: 100%;
            
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }

    &__content {
        padding: 52px;
        display: flex;
        flex-direction: column;
        background: var(--background-on-black-light-black) radial-gradient( rgba(222, 82, 62, 0.7) -10%, 15%, var(--background-on-black-light-black) 60%);
    }

    &__swiper {
        flex-grow: 1;
        margin-bottom: 72px;
        position: relative;

    }

    &__swiper-btns {
        display: flex;
        justify-content: space-between;
        width: 100%;
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0%, -50%);
        z-index: 5;
    }

    &__swiper-button-next, &__swiper-button-prev {
        border-radius: 10px;
        border: 1px solid var(--stroke-light-black);
        background: transparent;
        &::after {
            background-color: var(--stroke-dark-gray);
        }
    }

    &__swiper-slide {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: auto;
    }

    &__item {
        height: 100%;
        text-align: center;
        color: var(--text-primary-white);
        max-width: calc(100% - 184px);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        &-title {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 88px;
            line-height: 105%;
            text-transform: uppercase;
            text-align: center;
            color: var(--text-primary-white);
            margin-bottom: 40px;
        }
    }


    &-years {
        &__swiper {
            width: 100%;
            min-height: 114px;
            height: auto;
            overflow: visible;
            position: relative;
            padding-top: 24px;

            &::before {
                content: '';
                display: block;
                width: calc(100% + 120px);
                height: 1px;
                border-bottom: 4px dotted var(--background-on-black-gray);
                position: absolute;
                left: -52px;
                top: 0;
            }
        }

        &__swiper-slide {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 40px;
            line-height: 110%;
            text-transform: uppercase;
            color: var(--text-tertiary-white);
            transition: all 0.3s ease;
            position: relative;

            & span {
                display: block;
                text-align: center;
                position: relative;
                &::after {
                    content: '';
                    display: block;
                    width: 4px;
                    height: 4px;
                    border-radius: 100%;
                    background: var(--background-base-orange);
                    position: absolute;
                    left: 48%;
                    top: -24px;
                }
            }


            &.swiper-slide-thumb-active {
                font-size: 110px;
                line-height: 80%;
                color: var(--text-orange);
                & span {
                    display: block;
                    line-height: 80%;
                    &::after {
                        content: '';
                        display: block;
                        width: 20px;
                        height: 20px;
                        border: 5px solid var(--background-on-black-light-black);
                        outline: 1px solid var(--background-base-orange);
                        position: absolute;
                        left: 48%;
                        top: -30px;
                    }
                }
            }
        }
    }
}

@media (max-width: 1500px) {
    .history {
        &__swiper-button-next, &__swiper-button-prev {
            width: 55px;
            height: 55px;
        }
        &__item {
            max-width: calc(100% - 130px);
        }
        &__content {
            padding: 40px 25px;
        }
    }
}

@media (max-width: 1300px) {
    .history {
        &__row {
            flex-direction: column-reverse;
            gap: 10px;
            min-height: 100%;
        }

        &__card {
            max-width: 100%;
            min-height: auto;
        }

        &__content {
            min-height: 506px;
        }
    }
}

@media (max-width: 768px) {
    .history {
        &__title {
            margin: 0 auto 32px;
        }

        &__row {
            gap: 8px;
        }

        &__card {
            border-radius: 20px;
        }

        &__photo {
            &-pic {
                & img {
                    min-height: 340px;
                }
            }
        }

        &__content {
            padding: 24px;
            min-height: 506px;
            background: var(--background-on-black-light-black) radial-gradient( rgba(222, 82, 62, 0.7) -10%, 15%, var(--background-on-black-light-black) 60%);
        }

        &__swiper {
            flex-grow: 1;
            margin-bottom: 72px;
            position: relative;

        }

        &__swiper-btns {
            display: none;
        }

        &__swiper-slide {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            height: auto;
        }

        &__item {
            max-width: 100% ;
            &-title {
                font-size: 44px;
                margin-bottom: 20px;
            }
        }


        &-years {
            &__swiper {
                min-height: 70px;
            }
            &__swiper-slide {
                font-size: 28px;
                & span {
                    &::after {
                        left: 50%;
                    }
                }

                &.swiper-slide-thumb-active {
                    font-size: 56px;
                    & span {
                        &::after {
                            width: 14px;
                            height: 14px;
                            border: 4px solid var(--background-on-black-light-black);
                            left: 48%;
                            top: -28px;
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 500px) {
    .history {
        &__photo {
            &-pic {
                & img {
                    min-height: 280px;
                }
            }
        }
    }
}