
// technology
.technology {
    &__title {
        max-width: 800px;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
    }

    &__item {
        border-radius: 20px;
        border: 1px solid var(--stroke-light-black);
        min-height: 240px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 24px;

        &-pic {
            display: block;

        }
    }
}

@media (max-width: 1200px) {
    .technology {
        &__list {
            grid-template-columns: repeat(3, 1fr);
            gap: 20px;
        }
        &__item {
            padding: 20px 30px;
        }
    }
}
@media (max-width: 768px) {
    .technology {
        &__list {
            grid-template-columns: repeat(2, 1fr);
            gap: 10px;
        }
        &__item {
            padding: 10px 30px;
            border-radius: 7px;
            min-height: 160px;
        }
    }
}
@media (max-width: 400px) {
    .technology {
        &__item {
            min-height: 80px;
        }
    }
}