
// attraction
.attraction {
    position: relative;
    overflow: hidden;
    &::after {
        content: '';
        display: block;
        width: 189px;
        height: 1153px;
        border-radius: 100%;
        background: radial-gradient(rgba(255, 255, 255, 0.06) 20%, 40%, transparent 70%);
        position: absolute;
        pointer-events: none;
        top: -340px;
        left: 228px;
        transform: rotate(300deg);
    }

    &__wrap {
        position: relative;
    }

    &__title {
        max-width: 49%;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 10px 20px;
        position: absolute;
        top: 0;
        left: 0;

        &-icons {
            display: inline-flex;
        }

        &-icon {
            flex-shrink: 0;
            border-radius: 100%;
            background: #de523e;
            overflow: hidden;
            width: 72px;
            height: 72px;
            display: flex;
            align-items: center;
            justify-content: center;

            &:not(:first-child) {
                margin-left: -15px;
            }
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }

    &__card {
        border-radius: 30px;
        padding: 40px;
        background: var(--background-on-black-light-black);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        min-height: 440px;
        position: relative;
        overflow: hidden;

        &-dots {
            display: flex;
            gap: 4px;
            & span {
                display: block;
                width: 10px;
                height: 10px;
                border-radius: 100%;
                background: var(--background-base-orange);
            }
        }

        &-title {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 40px;
            line-height: 110%;
            text-transform: uppercase;
            color: var(--text-primary-white);
            margin-bottom: 24px;
        }

        &-bg {
            position: absolute;
            top: 0;
            right: 0;
        }
    }
}

@media (max-width: 1300px) {
    .attraction {
        overflow: hidden;
        &::after {
            content: none;
        }

        &__title {
            max-width: 100%;
            position: static;
            margin-bottom: 52px;

            &-icon {
                width: 66px;
                height: 66px;
            }
        }

        &__swiper {
            overflow: visible;
        }

        &__swiper-slide {
            height: auto !important;
            &:nth-child(1), &:nth-child(2) {
                display: none;
            }
        }

        &__card {
            min-height: 380px;
        }
    }
}

@media (max-width: 768px) {
    .attraction {
        &__wrap {
            position: relative;
        }
    
        &__title {
            gap: 6px 11px;
            margin-bottom: 32px;
    
            &-icon {
                width: 36px;
                height: 36px;
    
                &:not(:first-child) {
                    margin-left: -15px;
                }
            }
        }
    
        &__card {
            border-radius: 20px;
            padding: 24px;
            min-height: 240px;
    
            &-dots {
                & span {
                    width: 6px;
                    height: 6px;
                }
            }
    
            &-title {
                font-size: 32px;
                margin-bottom: 16px;
            }
        }
    }
}