
// job
.job {
    &__title {
        max-width: 1250px;
        margin: 0 auto 100px;
        justify-content: center;
        text-align: center;
        gap: 0 10px;
    }
    
    &__swiper {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
        & .swiper-wrapper {
            display: contents;
        }
    }

    &__swiper-slide {
        height: auto !important;
        display: contents;

        &:first-child .job__card{
            grid-column: span 2;
        }
        &:last-child .job__card{
            grid-column: span 2;
        }
    }

    &__card {
        border-radius: 20px;
        padding: 44px;
        background: var(--background-on-white-light-gray);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 60px;
        height: 100%;

        &-num {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 120px;
            opacity: 0.1;
            color: #1C1E28;
            line-height: 80%;
            text-align: right;
            position: relative;
            z-index: 2;

            &::after {
                content: attr(data-content);
                position: absolute;
                top: 12px;
                right: -12px;
                opacity: 0.22;
            }
        }

        &-text {
            color: var(--text-secondary-black);
        }

        &-title {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 40px;
            line-height: 1.1;
            text-transform: uppercase;
            color: var(--text-primary-black);
            margin-bottom: 12px;
            
            & span {
                color: var(--text-orange);
            }
        }
    }
}

@media (max-width: 1400px) {
    .job {
        &__swiper-pic {
            display: none;
        }

        &__swiper {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    
        &__swiper-slide {
            &:first-child .job__card{
                grid-column: span 1;
            }
            &:last-child .job__card{
                grid-column: span 1;
            }
        }
    }
}

@media (max-width: 768px) {
    .job {
        overflow: hidden;

        &__title {
            margin: 0 auto 32px;
        }
        
        &__swiper {
            display: block;
            overflow: visible;
            & .swiper-wrapper {
                display: flex;
            }
        }
    
        &__swiper-slide {
            height: auto !important;
            display: block;
        }

        &__swiper-pic {
            display: none;
        }
    
        &__card {
            border-radius: 20px;
            padding: 24px;
            gap: 50px;
            &-num {
                font-size: 80px;
                &::after {
                    top: 8px;
                    right: -8px;
                }
            }

            &-title {
                font-size: 28px;
                margin-bottom: 12px;
            }
        }
    }
}