
// cost
.cost {

    &__title {
        margin-bottom: 60px;
    }

    &__swiper {
    }

    &__swiper-slide {
        height: auto;
        &:last-child {
            & .cost__card-wrap {
                background: var(--background-base-orange);
            }
            & .cost__card-text {
                color: var(--text-secondary-orange);
                &::after {
                    border-color: var(--background-base-light-orange);
                }
            }
            & .cost__card-title {
                color: var(--text-primary-white);
            }
        }
    }

    &__card {
        border-radius: 30px;
        padding: 12px;
        background: var(--background-on-black-light-black);
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    &__card-wrap {
        border-radius: 18px;
        padding: 40px;
        background: var(--background-base-white);
        flex-grow: 1;
        overflow: hidden;
    }

    &__card-text {
        color: var(--text-secondary-black);
        padding-bottom: 32px;
        margin-bottom: 32px;
        position: relative;

        &::after {
            content: '';
            display: block;
            width: calc(100% + 80px);
            height: 4px;
            border-bottom: 4px dotted var(--background-on-white-gray);
            position: absolute;
            bottom: 0;
            left: -40px;
        }
    }

    &__card-title {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 56px;
        line-height: 1.14286;
        text-transform: uppercase;
        color: var(--text-primary-black);
        margin-bottom: 28px;
    }

    &__card-pic {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 350px;
    }

    &__card-bottom {
        padding: 40px;
    }

    &__card-price {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 40px;
        line-height: 1.1;
        text-transform: uppercase;
        color: var(--text-primary-white);
        margin-bottom: 16px;
    }

    &__card-footnote {
        font-family: var(--second-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 1.42857;
        color: var(--text-secondary-white);
        margin-bottom: 28px;
    }

    &__card-btn {
        width: 100%;
    }
}

@media (max-width: 1300px) {
    .cost {
        overflow: hidden;

        &__swiper {
            overflow: visible;
        }
        
        &__card-wrap {
            border-radius: 16px;
            padding: 20px;
        }
    
        &__card-title {
            font-size: 42px;
        }
    
        &__card-pic {
            height: 300px;
        }
    
        &__card-bottom {
            padding: 20px;
        }

        &__card-price {
            font-size: 36px;
        }
    }
}

@media (max-width: 768px) {
    .cost {
        &__title {
            margin-bottom: 32px;
        }
    
        &__card {
            border-radius: 20px;
            padding: 4px;
        }
    
        &__card-text {
            padding-bottom: 16px;
            margin-bottom: 16px;
        }
    
        &__card-title {
            font-size: 32px;
            margin-bottom: 16px;
        }
    
        &__card-pic {
            height: 120px;
            & img {
                max-height: 90%;
            }
        }
    
        &__card-price {
            font-size: 28px;
            margin-bottom: 12px;
        }
    
        &__card-footnote {
            font-size: 12px;
            margin-bottom: 16px;
        }
    
        &__card-btn {
            width: 100%;
        }
    }
}