
// intro
.intro {
    position: relative;

    &::after, &::before {
        content: '';
        display: block;
        width: 1153px;
        height: 153px;
        border-radius: 100%;
        background: radial-gradient(rgba(255, 255, 255, 0.04) 10%, 40%, transparent 80%);
        position: absolute;
        pointer-events: none;
        transition: all 0.9s ease;
    }
    &::after {
        top: -50px;
        right: 50%;
        transform: rotate(130deg) translateY(100%);
    }
    &::before {
        top: -50px;
        left: 50%;
        transform: rotate(-130deg) translateY(100%);
    }

    &.animation-active {
        &::after {
            top: 10px;
            right: 50%;
            transform: rotate(58deg) translateY(-50%);
        }
        &::before {
            top: 10px;
            left: 50%;
            transform: rotate(-58deg) translateY(-50%);
        }
    }
    
    &__wrap {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 32px;
        padding: 50px 0;
        min-height: calc(100vh - 112px);
    }

    &__title {
        display: block;
        width: 100%;
        & img {
            width: 100%;
            height: 100%;
        }
    }

    &__row {
        display: flex;
        align-items: end;
        justify-content: space-between;
        gap: 28px;
    }

    &__pic {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-42%, 0%);
        height: 100%;
        max-height: 90%;
        max-width: 35%;
        & img {
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: bottom;
        }
    }

    &__items {
        display: flex;
        flex-direction: column;
        gap: 62px;
        max-width: 440px;
    }

    &__item {
        display: flex;
        flex-direction: column;
        gap: 24px;
        font-size: 20px;
        line-height: 130%;
        color: var(--base-black-4);

        &-stick {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 28px;
            line-height: 114%;
            text-transform: uppercase;
            color: var(--base-orange-0);
        }
    }
}

.contacts-intro__wrap {
    padding: 180px 0 60px;
}

@media (max-width: 1300px) {
    .intro {
        &__pic {
            transform: translate(-30%, 0%);
        }

        &__items {
            max-width: 400px;
        }
    }

    .contacts-intro__wrap {
        padding: 120px 0 50px;
    }
}

@media (max-width: 1024px) {
    .intro {
        position: relative;
        &::after, &::before {
            content: none;
        }
        
        &__wrap {
            padding: 24px 0 72px;
        }

        &__title {
            display: block;
            width: 100%;
            & img {
                width: 100%;
                height: 100%;
            }
        }

        &__row {
            flex-direction: column;
            align-items: stretch;
        }

        &__pic {
            display: none;
        }

        &__items {
            max-width: 600px;
        }

        &__item {
            display: flex;
            flex-direction: column;
            gap: 24px;
            font-size: 20px;
            line-height: 130%;
            color: var(--base-black-4);

            &-stick {
                font-family: var(--font-family);
                font-weight: 500;
                font-size: 28px;
                line-height: 114%;
                text-transform: uppercase;
                color: var(--base-orange-0);
            }
        }
    }
}
@media (max-width: 768px) {
    .intro {
        &__wrap {
            min-height: auto;
        }

        &__items {
            gap: 24px;
        }

        &__item {
            gap: 16px;
            font-size: 14px;

            &-stick {
                max-width: 100px;
            }
        }
    }

    .contacts-intro__wrap {
        padding: 52px 0 32px;
    }
}