
.header {
    padding: 32px 0;

    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 20px;
    }

    &__nav {
        &-list {
            display: flex;
            gap: 40px;
        }

        &-link {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 28px;
            line-height: 114%;
            text-transform: uppercase;
            color: var(--text-primary-white);
            transition: all 0.3s ease;
            &:hover {
                color: var(--background-base-orange);
            }
        }

        &-btn {
            display: block;
            position: relative;
            padding-right: 30px;
        }

        &-arrow {
            flex-shrink: 0;
            width: 20px;
            height: 20px;
            position: absolute;
            top: 50%;
            right: 0;
            transform: translate(0%, -50%);
            transition: all 0.3s ease;
            & svg > * {
                transition: all 0.3s ease;
            }
        }

        &-submenu {
            position: absolute;
            top: 100%;
            left: -32px;
            min-width: 340px;
            opacity: 0;
            z-index: 3;
            visibility: hidden;
            pointer-events: none;
            transition: all 0.3s ease;

            & ul {
                border: 1px solid var(--stroke-light-black);
                border-radius: 20px;
                background: var(--background-on-black-light-black);
                margin-top: 24px;
            }

            & li {
                & .header__nav-arrow {
                    position: static;
                    top: 10px;
                    right: 30px;
                }

                &:not(:last-child) {
                    border-bottom: 1px solid var(--stroke-light-black);
                }
            }

            & a {
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 10px;
                padding: 32px;
                font-family: var(--font-family);
                font-weight: 500;
                font-size: 28px;
                line-height: 114%;
                text-transform: uppercase;
                color: var(--text-primary-white);
                transition: all 0.3s ease;
                & svg path {
                    transition: all 0.3s ease;
                }
                &:hover {
                    color: var(--text-orange);
                    & svg path {
                        stroke: var(--text-orange);
                    }
                }
            }
        }
    }

    &__bottom {
        display: none;
    }
}

@media (hover :hover) {
    .header {
        &__nav {
            &-btn:hover {
                & .header__nav-submenu {
                    opacity: 1;
                    visibility: visible;
                    pointer-events: all;
                }

                & .header__nav-link {
                    color: var(--background-base-orange);
                }

                & > .header__nav-arrow {
                    transform: translate(0%, -50%) rotate(180deg);
                    & svg path {
                        stroke: var(--stroke-white);
                    }
                    & svg rect {
                        fill: var(--background-base-orange);
                        fill-opacity: 1;
                    }
                }
            }
        }
    }
}

.burger {
    border-radius: 10px;
    padding: 13px 10px 13px 12px;
    background: var(--background-on-black-light-black);
    width: 40px;
    height: 40px;
    display: flex;
    flex-direction: column;
    align-items: end;
    justify-content: center;
    gap: 4px;
    display: none;

    & span {
        display: block;
        border-radius: 10px;
        width: 18px;
        height: 2px;
        background: var(--background-base-white);
        transition: all 0.3s ease;

        &:nth-child(2) {
            width: 12px;
        }
        &:nth-child(3) {
            width: 17px;
        }
    }

    &.active {
        padding: 13px;
        & span {
            &:nth-child(1) {
                width: 20px;
                transform: rotate(45deg) translate(5px, 0px);
            }
            &:nth-child(2) {
                display: none;
            }
            &:nth-child(3) {
                width: 20px;
                transform: rotate(-45deg) translate(4px, 1px);
            }
        }
    }
}


@media (max-width: 1024px) {
    .header {
        padding: 20px 0;

        &__nav {
            &-list {
                gap: 26px;
            }

            &-link {
                font-size: 28px;
            }
        }
    }
}

@media (max-width: 768px) {
    main {
        margin-top: 84px;
    }

    .header {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 12;
        background: rgba(28, 30, 40, 0.85);
        backdrop-filter: blur(18px);
        padding: 24px 0 20px;
        transition: all 0.3s ease;
        
        &.header--active {
            background: var(--background-base-black);
        }

        &__row {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 20px;
        }

        &__logo {
            display: block;
            max-width: 68px;
        }

        &__btn {
            display: none;
        }

        &__menu {
            position: absolute;
            top: 83px;
            left: 0;
            width: 100%;
            height: calc(100vh - 83px);
            opacity: 0;
            z-index: 20;
            visibility: hidden;
            pointer-events: none;
            background: var(--background-base-black);
            transition: all 0.3s ease;

            &.open {
                opacity: 1;
                visibility: visible;
                pointer-events: all;
            }
        }

        &__nav {

            border-radius: 20px;
            padding: 40px 10px;
            background: var(--background-on-black-light-black);

            &-list {
                flex-direction: column;
                gap: 24px;
                padding-bottom: 30px;
                margin-bottom: 32px;
                position: relative;
                &::after {
                    content: '';
                    display: block;
                    width: calc(100% + 30px);
                    height: 4px;
                    position: absolute;
                    bottom: 0;
                    left: -15px;
                    border-bottom: 4px dotted var(--background-on-black-gray);
                }
            }

            &-link {
                flex-grow: 1;
                font-size: 24px;
                text-align: left;
            }

            &-btn {
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                justify-content: space-between;
                padding-right: 0;
                &.hide-item--active {
                    & .header__nav-link {
                        color: var(--text-orange);
                    }
                    & .header__nav-arrow {
                        transform: rotate(180deg);
                        & svg path {
                            stroke: var(--stroke-white);
                        }
                        & svg rect {
                            fill: var(--background-base-orange);
                            fill-opacity: 1;
                        }
                    }
                    & .header__nav-submenu {
                        opacity: 1;
                        visibility: visible;
                        pointer-events: all;
                    }
                }
            }

            &-arrow {
                position: static;
                transform: none;
            }

            &-submenu {
                position: static;
                min-width: auto;
                width: 100%;
                flex: 1 0 100%;
                max-height: 0;
                transition: all 0.3s ease;

                & ul {
                    border-radius: 10px;
                    margin-top: 16px;
                    margin-bottom: 1px;
                }

                & li {
                    & svg {
                        height: 12px;
                        width: 7px;
                    }
                }

                & a {
                    padding: 20px 16px;
                    font-size: 18px;
                    line-height: 114%;
                }
            }
        }


        &__bottom {
            display: block;
        }

        &__contacts {
            display: flex;
            flex-direction: column;
            gap: 16px;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 28px;
            line-height: 1.1;
            text-transform: uppercase;
            color: var(--base-orange-0);
            margin-bottom: 40px;
            
            & a {
                color: var(--base-orange-0);
                text-decoration: underline;
                transition: all 0.3s ease;
                &:hover {
                    color: var(--stroke-orange);
                }
            }
        }
    
        &__networks {
            display: flex;
            flex-direction: column;
            gap: 16px;
        }
    }

    .burger {
        display: flex;
    }
}