
// mission
.mission {
    overflow: hidden;
    padding-bottom: 27px;

    &__title {
        color: var(--text-primary-black);
        padding-bottom: 60px;
        border-bottom: 4px dotted var(--background-on-white-gray);

        & span {
            position: relative;
            padding-right: 152px;
            &::after {
                content: '';
                display: inline-block;
                width: 40px;
                height: 49px;
                background-image: url("data:image/svg+xml,%3Csvg width='40' height='49' viewBox='0 0 40 49' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M10.2084 1.47155C10.6138 0.576357 11.5173 0 12.5184 0H32.4824C34.3594 0 35.5832 1.93754 34.7423 3.58568L29.0637 14.7155H37.4677C39.7202 14.7155 40.8464 17.3839 39.2547 18.9438L9.33996 48.2595C7.45293 50.1087 4.30954 48.2055 5.15546 45.726L11.5323 26.9784H2.52763C2.10653 26.9784 1.69212 26.8753 1.32202 26.6785C0.951918 26.4816 0.637861 26.1973 0.408371 25.8513C0.178881 25.5053 0.0412297 25.1086 0.00792093 24.6972C-0.0253878 24.2859 0.0467011 23.8729 0.217639 23.4957L10.2084 1.47155Z' fill='%23DE523E'/%3E%3C/svg%3E");
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
                position: absolute;
                top: 50%;
                right: 56px;
                transform: translate(0%, -50%);
            }
        }
    }

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 74px;
        position: relative;
    }

    &__line {
        display: block;
        width: 4px;
        height: 100%;
        transform: translate(-50%, 0);
        border-right: 4px dotted var(--background-on-white-gray);
        position: absolute;
        top: 0;
        left: 50%;
    }

    &__block {
        flex: 1 0 40%;
        border-radius: 20px;
        padding: 64px;
        position: relative;
        margin-top: 39px;
        min-height: 780px;
        overflow: hidden;

        &-title {
            border-radius: 10px;
            padding: 20px 28px;
            display: flex;
            align-items: center;
            gap: 16px;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 40px;
            line-height: 110%;
            text-transform: uppercase;
            width: fit-content;
            margin-bottom: 32px;
        }
    }
    
    &__black {
        background: var(--background-base-black) url('./../img/mission/mission-bg.svg') no-repeat bottom center / 100% auto;
        & .mission__block-title {
            background: var(--background-on-black-light-black);
            color: var(--text-primary-white);
        }

        &-text {
            color: var(--text-primary-white);
        }

        &-img {
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, 0%);
            max-height: 460px;
        }
    }

    &__white {
        background: var(--background-on-white-light-gray);
        & .mission__block-title {
            background: var(--background-base-white);
            color: var(--text-primary-black);
        }

        &-text {
            color: var(--text-primary-black);
        }

        &-img {
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, 0%);
            width: 100%;
            max-height: 432px;
        }
    }
}

@media (max-width: 1340px) {
    .mission {
        &__block {
            padding: 44px;
            min-height: 600px;
            &-title {
                padding: 16px 22px;
                font-size: 32px;
            }
        }
    }
}
@media (max-width: 1024px) {
        .mission {
        padding-bottom: 40px;

        &__title {
            & span {
                padding-right: 105px;
                &::after {
                    width: 35px;
                    height: 49px;
                    right: 35px;
                }
            }
        }

        &__row {
            flex-direction: column;
            gap: 8px;
            margin-top: 32px;
        }

        &__line {
            display: none;
        }

        &__block {
            margin-top: 0;
        }

        &__black {
            &-img {
                max-height: 360px;
            }
        }
        &__white {
            &-img {
                max-height: 360px;
            }
        }
    }
}
@media (max-width: 768px) {
    .mission {
        padding-bottom: 40px;

        &__title {
            padding-bottom: 34px;
            & span {
                padding-right: 48px;
                &::after {
                    width: 16px;
                    height: 20px;
                    right: 16px;
                }
            }
        }

        &__block {
            border-radius: 10px;
            padding: 24px;
            margin-top: 0;
            min-height: auto;

            &-title {
                padding: 16px 20px;
                gap: 12px;
                font-size: 24px;
                margin-bottom: 20px;
                & svg {
                    max-width: 11px;
                }
            }
        }
        
        &__black {
            padding-bottom: 300px;
            &-img {
                max-height: 280px;
            }
        }

        &__white {
            padding-bottom: 270px;
            &-img {
                max-height: 250px;
            }
        }
    }
}

@media (max-width: 500px) {
    .mission {
        &__black {
            padding-bottom: 230px;
            &-img {
                max-height: 213px;
            }
        }

        &__white {
            padding-bottom: 180px;
            &-img {
                max-height: 160px;
            }
        }
    }
}