/* swiper CUSTOM */

.swiper {
    width: 100%;
    height: 100%;
}

/* swiper-buttons */
.swiper-btns {
    display: flex;
    align-items: center;
}
.swiper-button-next, .swiper-button-prev {
    position: static;
    margin: 0;
    flex-shrink: 0;
    width: 72px;
    height: 72px;
    background: var(--background-on-white-light-gray);
    transition: all 0.3s ease;

    &.durk {
        background: var(--background-on-black-light-black);
        &::after {
            background-color: var(--stroke-white);
        }
    }
}
.swiper-button-prev {
    border-radius: 10px 0 0 10px;
    border-right: 1px solid var(--stroke-gray);
    &.durk {
        border-right: 1px solid var(--stroke-light-black);
    }

    &::after {
        content: '';
        display: inline-block;
        width: 15px;
        height: 16px;
        background-color: #1C1E28; 
        -webkit-mask-image: url("data:image/svg+xml,%3Csvg width='15' height='16' viewBox='0 0 15 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 7.71387L15 7.71387M1 7.71387L8.12727 0.713867M1 7.71387L8.12727 14.7139' stroke='black' stroke-width='2' stroke-linejoin='round'/%3E%3C/svg%3E");
        mask-image: url("data:image/svg+xml,%3Csvg width='15' height='16' viewBox='0 0 15 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 7.71387L15 7.71387M1 7.71387L8.12727 0.713867M1 7.71387L8.12727 14.7139' stroke='black' stroke-width='2' stroke-linejoin='round'/%3E%3C/svg%3E");
        -webkit-mask-repeat: no-repeat;
        mask-repeat: no-repeat;
        -webkit-mask-position: center;
        mask-position: center;
        -webkit-mask-size: contain;
        mask-size: contain;
        transition: all 0.3s ease;
    }
}
.swiper-button-next {
    border-radius: 0 10px 10px 0;

    &::after {
        content: '';
        display: inline-block;
        width: 15px;
        height: 16px;
        background-color: #1C1E28; 
        -webkit-mask-image: url("data:image/svg+xml,%3Csvg width='15' height='16' viewBox='0 0 15 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14 7.71387L-5.41851e-08 7.71387M14 7.71387L6.87273 0.713867M14 7.71387L6.87273 14.7139' stroke='black' stroke-width='2' stroke-linejoin='round'/%3E%3C/svg%3E");
        mask-image: url("data:image/svg+xml,%3Csvg width='15' height='16' viewBox='0 0 15 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14 7.71387L-5.41851e-08 7.71387M14 7.71387L6.87273 0.713867M14 7.71387L6.87273 14.7139' stroke='black' stroke-width='2' stroke-linejoin='round'/%3E%3C/svg%3E");
        -webkit-mask-repeat: no-repeat;
        mask-repeat: no-repeat;
        -webkit-mask-position: center;
        mask-position: center;
        -webkit-mask-size: contain;
        mask-size: contain;
        transition: all 0.3s ease;
    }
}
.swiper-button-next:hover:after, .swiper-button-prev:hover:after {
    background: var(--stroke-orange);
}


/* swiper-buttons--white */
.swiper-button-next--white, .swiper-button-prev--white {
    background: var(--belyy);
}
.swiper-button-next--white:after, .swiper-button-prev--white:after {
    color: var(--color-temno-zelenyy);
}
.swiper-button-next--white:hover,.swiper-button-prev--white:hover {
    background: rgba(61, 124, 133, 0.25);
}
.swiper-button-next--white:hover:after, .swiper-button-prev--white:hover:after {
    color: var(--color-temno-zelenyy);
}


/* swiper-pagination  */
.swiper-pagination {
    position: static;
    width: auto !important;

    &-bullet {
        position: static;
        border-radius: 10px;
        width: 25px;
        height: 3px;
        background: var(--color-zelenyy);
    }
}

.swiper-pagination-fraction {
    font-weight: 700;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.5);

    .swiper-pagination-current {
        font-weight: 700;
        font-size: 26px;
        color: var(--belyy);
    }
}

.swiper-scrollbar {
    position: static !important;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    &-drag {
        background: var(--belyy);
        border-radius: 10px;
    }
}
.swiper-scrollbar-temnyy {
    position: static !important;
    background: var(--izobrazhenie);
    .swiper-scrollbar-drag {
        background: var(--color-zelenyy);
    }
}
