
// faq
.faq {

    &__list {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &__item {
        border-radius: 20px;
        padding: 34px 48px;
        padding-left: 116px;
        background: var(--background-on-black-light-black);
        position: relative;

        &::before {
            content: '';
            display: block;
            width: 44px;
            height: 44px;
            background: url('./../img/icons/faq-sticker.png') no-repeat center / contain;
            position: absolute;
            top: 48px;
            left: 48px;
        }

        &.accordion--active {
            & .faq__item-btn {
                transform: rotate(90deg);
            }
        }
    }

    &__question {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        gap: 20px;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 56px;
        line-height: 1.14286;
        text-transform: uppercase;
        color: var(--text-primary-white);
    }

    &__item-btn {
        flex-shrink: 0;
        transition: all 0.3s ease;
    }

    &__answer {
        max-width: 1380px;
        color: var(--text-secondary-white);
        padding-top: 40px;
    }
}

@media (max-width: 1300px) {
    .faq {
        &__question {
            font-size: 44px;
        }
    }
}

@media (max-width: 768px) {
    .faq {

        &__list {
            gap: 8px;
        }
    
        &__item {
            border-radius: 10px;
            padding: 20px;
    
            &::before {
                content: none;
            }
        }
    
        &__question {
            align-items: center;
            gap: 8px;
            font-size: 24px;
        }
    
        &__item-btn {
            max-width: 36px;
        }
    
        &__answer {
            padding-top: 20px;
        }
    }
}