.popup {
    height: 100vh;
    left: 50%;
    opacity: 0;
    pointer-events: none;
    position: fixed;
    top: 50%;
    transform: translate3d(-50%, -50%, 0);
    width: 100%;
    z-index: 2000;
    transition: opacity 0.4s ease;
    overflow-y: auto;
    background: rgba(0, 8, 84, 0.27);
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: blur(4px);
    display: none;
}

.popup.active {
    display: block;
}

.popup__body {
    width: 100%;
    min-height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 35px 15px 20px;
}

.popup.open {
    opacity: 1;
    pointer-events: all;
}

.popup__content {
    width: 100%;
    max-width: 820px;
    border-radius: 30px;
    padding: 40px 60px;
    position: relative;
    scale: 0.3;
    transition: scale 0.4s ease;
    background: var(--background-on-black-light-black);
}

.popup.open .popup__content {
    scale: 1;
}

.close-popup {
    position: absolute;
    top: 32px;
    right: 32px;
    width: 20px;
    height: 20px;
    cursor: pointer;
    transition: all 0.3s linear;
}

@media (hover: hover) {
    .close-popup:hover {
        transform: scale(1.2);
    }
}

.popup__title {
    margin-bottom: 10px;
}

.popup__text {
    color: var(--text-secondary-white);
    margin-bottom: 30px;
}

.popup__form {
    display: flex;
    gap: 10px;
    & > * {
        width: 100%;
    }
    &-btn {
        width: fit-content;
    }
}

.popup-change {
    &__form {
        flex-direction: column;
        & .politic {
            margin: 12px 0;
        }
    }
}

.popup-job {
    &__content {
        max-width: 1000px;
    }

    &__form {
        flex-wrap: wrap;
        align-items: center;

        & > * {
            flex: 1 0 40%;
        }

        & > textarea {
            flex: 1 0 100%;
        }

        &-resume {
            display: flex;
            align-items: center;
            gap: 16px;
            margin: 20px 0;

            &-btn {
                border-radius: 20px;
                background: var(--background-on-black-gray);
                border: none;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 77px;
                height: 77px;
                padding: 0;
                position: relative;
            }
            
            & input[type=file] {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                opacity: 0;
            }

            & label {
                display: block;
                font-family: var(--font-family);
                font-weight: 500;
                font-size: 28px;
                line-height: 1.14286;
                text-transform: uppercase;
                color: var(--text-primary-white);
                margin-bottom: 6px;
            }

            & span {
                font-size: 18px;
                line-height: 1.22222;
                color: var(--text-secondary-white);
            }
            
            & .file-item {
                display: flex;
                align-items: center;
                gap: 8px;
            }
            & .file-remove {
                cursor: pointer;
            }
        }
    }
}


.popup-response {
    &__content {
        max-width: 600px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__pic {
        display: block;
        width: fit-content;
        margin-bottom: 32px;
    }

    &__wrapper {
        color: var(--text-secondary-white);
        margin-bottom: 40px;
        text-align: center;
        & h3 {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 56px;
            line-height: 1.14286;
            text-transform: uppercase;
            text-align: center;
            color: var(--text-primary-white);
            margin-bottom: 32px;
        }
    }
}


@media (max-width: 768px) {
    .popup__body {
        padding: 20px 10px;
    }
    
    .popup__content {
        border-radius: 20px;
        padding: 40px 20px;
    }
    
    .close-popup {
        top: 16px;
        right: 16px;
        width: 12px;
        height: 12px;
    }
    
    .popup__title {
        margin-bottom: 10px;
    }
    
    .popup__text {
        margin-bottom: 18px;
    }
    
    .popup__form {
        flex-direction: column;

        &-btn {
            width: 100%;
            order: 3;
        }
    }

    .popup-change {
        &__form {
            & .politic {
                margin-bottom: 0;
            }
        }
    }

    .popup-job {
        &__form {
            &-resume {
                gap: 12px;
                margin: 12px 0;
    
                &-btn {
                    border-radius: 10px;
                    width: 58px;
                    height: 58px;
                }
    
                & label {
                    font-size: 24px;
                    margin-bottom: 6px;
                }
    
                & span {
                    font-size: 14px;
                }
            }
    
            & .politic {
                order: 2;
                margin-bottom: 12px;
            }
        }
    }

    .popup-response {
        &__pic {
            max-width: 100px;
            margin-bottom: 28px;
            & img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }
    
        &__wrapper {
            margin-bottom: 28px;
            & h3 {
                font-size: 32px;
                margin-bottom: 16px;
            }
        }

        &__btn {
            width: 100%;
        }
    }
    
}
