
// advantages
.advantages {
    counter-reset: itemm;
    
    &__swiper .swiper-wrapper {
        list-style-type: none;
    }

    &__swiper-slide {
        height: auto;
        position: relative;

        &::before {
            content: counter(itemm, decimal-leading-zero);
            counter-increment: itemm;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 50px;
            line-height: 1.1;
            text-transform: uppercase;
            position: absolute;
            left: 50%;
            top: 165px;
            transform: translate(-50%, 0);
            color: var(--background-base-white);
            opacity: 0.2;
            z-index: 2;
        }

    }
    
    &__card {
        border-radius: 30px;
        padding: 40px;
        background: var(--background-base-orange);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 32px;
        height: 100%;
        min-height: 440px;
        overflow: hidden;
        position: relative;

        &::after {
            content: counter(item, decimal-leading-zero);
            counter-increment: item;
            display: block;
            width: 700px;
            height: 700px;
            position: absolute;
            left: 50%;
            top: 215px;
            transform: translate(-50%, 0);
            border-radius: 50%;
            box-shadow: 0px 26px 51px 20px rgba(255, 255, 255, 0.12) inset;
        }

        &-title {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 40px;
            line-height: 1.1;
            text-transform: uppercase;
            color: var(--text-primary-white);
            padding-bottom: 32px;
            position: relative;

            &::after {
                content: '';
                display: block;
                width: calc(100% + 80px);
                height: 1px;
                position: absolute;
                left: -40px;
                bottom: 0;
                border-bottom: 4px dotted var(--background-base-light-orange);
            }
        }

        &-text {
            font-size: 18px;
            line-height: 1.22222;
            color: var(--text-secondary-orange);
        }
    }
}

@media (max-width: 1300px) {
    .advantages {
        overflow: hidden;
        &__swiper {
            overflow: visible;
        }
    }
}
@media (max-width: 768px) {
    .advantages {
    
        &__swiper-slide {
            &::before {
                font-size: 37px;
                top: 107px;
            }
        }
        
        &__card {
            border-radius: 20px;
            padding: 24px;
            min-height: 300px;
    
            &::after {
                width: 680px;
                height: 510px;
                top: 143px;
            }
    
            &-title {
                font-size: 28px;
                padding-bottom: 24px;
            }
    
            &-text {
                font-size: 14px;
            }
        }
    }
}