
/*  glob */

html.lock {
    overflow: hidden;
}
html.burger-lock {
    overflow: hidden;
}
html::before {
    display: block;
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3215686275);
    position: fixed;
    inset: 0;
    opacity: 0;
    z-index: 10;
    pointer-events: none;
    -webkit-transition: opacity 0.3s linear;
    -o-transition: opacity 0.3s linear;
    transition: opacity 0.3s linear;
}
.no-scroll {
    overflow: hidden;
}
body.lock {
    overflow: hidden;
}
body.burger-lock {
    overflow: hidden;
}

html {
    overflow-x: hidden;
    overflow-y: auto;
}
body {
    margin: 0;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 0;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: var(--text-secondary-white);
    background: var(--background-base-black);
}
.container {
	margin: 0 auto;
	padding: 0 var(--container-padding);
	max-width: var(--container-width);
	width: 100%;
}

.section-offset {
    margin-bottom: 180px;
}

.white-block {
    border-radius: 30px;
    background: var(--background-base-white);
    padding: 65px 0;
    margin-right: 15px;
    margin-left: 15px;
    color: var(--text-primary-black);

    .section__top-title {
        color: var(--text-primary-black);
    }

    & .counter-blocks {
        & span {
            &:first-child {
                border-color: var(--stroke-white);
                background: var(--background-on-white-light-gray);
                color: var(--background-base-black);
            }

            &:last-child {
                background: var(--background-base-black);
                color: var(--text-primary-white);
            }
        }
    }
}

.section__title {
    margin-bottom: 60px;
}

.section__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 72px;

    &-title {
        font-family: var(--second-family);
        font-weight: 500;
        font-size: 14px;
        line-height: 143%;
        text-transform: uppercase;
        color: var(--text-primary-white);
        display: flex;
        align-items: center;
        gap: 8px;

        &::before {
            content: '';
            display: inline-block;
            width: 11px;
            height: 20px;
            background-color: #DE523E; /*   */
            -webkit-mask-image: url("data:image/svg+xml,%3Csvg width='11' height='20' viewBox='0 0 11 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.71429 4.8C7.43757 1.94123 11 0 11 0C11 0 8.72128 3.68128 7.46429 5.6C6.20729 7.51872 5.5 8.8 5.5 8.8L10.6071 10C10.6071 10 7.30302 14.2404 5.5 16C3.21665 18.2284 0 20 0 20C0 20 2.56305 15.929 3.53571 14.4C4.32143 13.1649 5.10714 12 5.10714 12L0 10.8C0 10.8 1.89103 7.76371 4.71429 4.8Z' fill='black'/%3E%3C/svg%3E");
            mask-image: url("data:image/svg+xml,%3Csvg width='11' height='20' viewBox='0 0 11 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.71429 4.8C7.43757 1.94123 11 0 11 0C11 0 8.72128 3.68128 7.46429 5.6C6.20729 7.51872 5.5 8.8 5.5 8.8L10.6071 10C10.6071 10 7.30302 14.2404 5.5 16C3.21665 18.2284 0 20 0 20C0 20 2.56305 15.929 3.53571 14.4C4.32143 13.1649 5.10714 12 5.10714 12L0 10.8C0 10.8 1.89103 7.76371 4.71429 4.8Z' fill='black'/%3E%3C/svg%3E");
            -webkit-mask-repeat: no-repeat;
            mask-repeat: no-repeat;
            -webkit-mask-position: center;
            mask-position: center;
            -webkit-mask-size: contain;
            mask-size: contain;
        }
    }
}

.counter-blocks {
    display: flex;
    & span {
        border-radius: 30px;
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: var(--second-family);
        font-weight: 500;
        font-size: 14px;
        line-height: 143%;
        text-transform: uppercase;
        
        &:first-child {
        width: 48px;
        height: 48px;
            border: 4px solid var(--stroke-black);
            background: var(--background-on-black-light-black);
            color: var(--text-primary-white);
            position: relative;
            right: -8px;
            top: -4px;
        }

        &:last-child {
            background: var(--background-base-white);
            color: var(--text-primary-black);
        }
    }
}

@media (max-width: 1300px) {
    body {
        font-size: 16px;
    }
    .section-offset {
        margin-bottom: 120px;
    }
}
@media (max-width: 768px) {
    body {
        font-size: 14px;
    }
    .container {
        padding: 0 10px;
    }

    .section-offset {
        margin-bottom: 72px;
    }

    .section__title {
        margin-bottom: 32px;
    }
    
    .section__top {
        margin-bottom: 28px;
        &-title {
            font-size: 10px;
            &::before {
                width: 9px;
                height: 16px;
            }
        }
    }

    .counter-blocks {
        & span {
            width: 28px;
            height: 28px;
            font-size: 10px;
            
            &:first-child {
                width: 32px;
                height: 32px;
                border-width: 2px;
                right: -6px;
                top: -2px;
            }
        }
    }

    .white-block {
        border-radius: 20px;
        padding: 40px 0;
        margin-right: 0;
        margin-left: 0;
    }
}



/*  buttons */
.btn-red, .btn-white {
    border-radius: 20px;
    padding: 40px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 28px;
    line-height: 114%;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    transition: all 0.3s ease;
    min-width: 348px;
}
.btn-red {
    background: var(--state-orange);
    color: var(--state-white);
    & svg path {
        fill: var(--state-white);
    }
}
.btn-white {
    background: var(--state-white);
    color: var(--text-primary-black);
    & svg path {
        fill: var(--text-primary-black);
        transition: all 0.3s ease;
    }
}

.btn-simple {
    border-radius: 20px;
    padding: 32px 56px;
    background: var(--state-orange);
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 28px;
    line-height: 114%;
    text-transform: uppercase;
    color: var(--state-white);
    transition: all 0.3s ease;
}

.btn-brief {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 28px;
    line-height: 114%;
    text-transform: uppercase;
    color: var(--text-primary-white);
    display: flex;
    align-items: center;
    gap: 12px;
    transition: all 0.3s ease;
    
    &::before {
        content: '[';
        color: var(--background-on-black-gray);
        transition: all 0.3s ease;
    }
    &::after {
        content: ']';
        color: var(--background-on-black-gray);
        transition: all 0.3s ease;
    }

    &:hover {
        color: var(--state-orange);
        &::before, &::after {
            color: var(--state-orange);
        }
    }
}

.network {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 40px;
    line-height: 114%;
    text-transform: uppercase;
    color: var(--text-primary-white);
    display: flex;
    align-items: center;
    gap: 12px;
    transition: all 0.3s ease;

    & svg path {
        transition: all 0.3s ease;
    }
    
    &::before {
        content: '[';
        color: var(--background-on-black-gray);
        transition: all 0.3s ease;
    }
    &::after {
        content: ']';
        color: var(--background-on-black-gray);
        transition: all 0.3s ease;
    }

    &:hover {
        color: var(--state-orange);
        &::before, &::after {
            color: var(--state-orange);
        }
        & svg path {
            fill: var(--state-orange);
        }
    }
}

@media (hover: hover) {
    .btn-red:hover {
        background: var(--state-orange-hover);
    }
    .btn-white:hover {
        color: var(--text-orange);
        & svg path {
            fill: var(--text-orange);
        }
    }
    .btn-simple:hover {
        background: var(--state-orange-hover);
    }
}

@media (max-width: 1220px) {
    .btn-red, .btn-white {
        border-radius: 20px;
        padding: 30px;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 28px;
        min-width: 270px;
    }
    .btn-simple {
        padding: 28px 30px;
    }
}
@media (max-width: 768px) {
    .btn-red, .btn-white {
        border-radius: 10px;
        padding: 28px;
        font-size: 24px;
        min-width: auto;
        & svg {
            width: 20px;
            height: 20px;
        }
    }
    .btn-simple {
        border-radius: 10px;
        padding: 24px;
        font-size: 18px;
    }

    .network {
        font-size: 28px;
        & svg {
            max-width: 20px;
        }
    }
}

.btn-more {
    &::after {
        content: "";
        display: inline-block;
        width: 7px;
        height: 7px;
        border-bottom: 1px solid var(--stroke-white);
        border-right: 1px solid var(--stroke-white);
        transform: rotate(45deg);
        margin-top: -4px;
        transition: all 0.3s ease;
    }

    &.active {
        &::after {
            transform: rotate(225deg);
            margin-top: 4px;
        }
    }
}




