/*  form  */
input, textarea {
    border: 1px solid var(--stroke-light-black);
    border-radius: 20px;
    padding: 32px;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 122%;
    color: var(--text-primary-white);
    outline: none;
    transition: all 0.3s ease;
    &::placeholder {
        font-family: var(--second-family);
        font-weight: 400;
        font-size: 18px;
        line-height: 122%;
        color: var(--text-secondary-white);
    }
    &:hover, &:focus, &:active {
        border: 1px solid var(--stroke-dark-gray);
    }
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	-webkit-appearance:none;
}

textarea {
    width: 100%;
    min-height: 200px;
    resize: none;
}

.politic {
    display: flex;
    align-items: start;
    gap: 10px;
    font-size: 14px;
    line-height: 140%;
    color: var(--text-secondary-white);
    & a {
        text-decoration: underline;
        color: var(--text-primary-white);
        transition: all 0.3s ease;
        &:hover {
            opacity: 0.5;
        }
    }

    & input[type="checkbox"] {
        flex-shrink: 0;
        position: relative;
        width: 20px;
        height: 20px;
        color: #000;
        border: 1px solid var(--background-base-white);
        border-radius: 4px;
        appearance: none;
        outline: 0;
        cursor: pointer;
        transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
        &::before {
            position: absolute;
            content: '';
            display: block;
            top: 50%;
            left: 50%;
            transform: translate(-45%, -60%) rotate(45deg);
            width: 6px;
            height: 12px;
            border-style: solid;
            border-color: var(--background-base-orange);
            border-width: 0 2px 2px 0;
            opacity: 0;
        }
        &:checked {
            color: #fff;
            background: var(--background-base-white);
            &::before {
                opacity: 1;
            }
        }
    }
}

@media (max-width: 768px) {
    input, textarea {
        border-radius: 10px;
        padding: 24px;
        font-size: 14px;
        &::placeholder {
            font-size: 14px;
        }
    }
    textarea {
        min-height: 140px;
    }
    .politic {
        font-size: 12px;
    }
}


/* select */
select {
    width: 100%;
    max-width: 100%;
    cursor: pointer;
    outline: none;
    border-radius: 5px;
    padding: 16px 20px;
    padding-top: 28px;
    font-weight: 500;
    color: var(--tekst);
    opacity: 0.7;
    padding-right: 30px;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    background: var(--fon-plashek) url(./../img/icons/arrow-bottom.svg) no-repeat right;
    background-position-x: calc(100% - 8px);
    transition: all 0.3s ease;

    &:hover {
        opacity: 1;
    }
    &:focus {
        outline: 1px solid var(--color-zelenyy);
    }
}




/* accordion */
.accordion {
    -webkit-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
    cursor: pointer;
    width: 100%;
    position: relative;

    &--active {
        .accordion__btn {
            transform: rotate(180deg);
        }
    }

    &__content {
        max-height: 0;
        overflow: hidden;
        -webkit-transition: all 0.4s ease;
        -o-transition: all 0.4s ease;
        transition: all 0.4s ease;
    }
}

/* tabs */
.tab-btn {
    transition: all 0.3s ease;
}
.tab-content {
    display: none;
    &.active {
        display: block;
        width: 100%;
        min-height: 100%;
    }
}



/* breadcrumbs */
.breadcrumbs{
    margin-bottom: 40px;
    justify-content: center;
}
.breadcrumbs, .breadcrumbs li {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    gap: 8px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.breadcrumbs li, .breadcrumbs li a {
    font-weight: 500;
    font-size: 14px;
    line-height: 1.42857;
    text-transform: uppercase;
    color: var(--text-primary-white);
}
.breadcrumbs li a {
    color: var(--text-secondary-black);
    transition: all 0.3s ease;
}
.breadcrumbs li a:hover {
    color: var(--text-primary-white);
}
.breadcrumbs li:after {
    content: "";
    display: block;
    width: 10px;
    height: 2px;
    border-top: 2px dotted var(--background-base-orange);
}
.breadcrumbs li:last-child::after {
    content: none;
}

@media (max-width: 768px) {
    .breadcrumbs{
        margin-bottom: 24px;
    }
    .breadcrumbs li, .breadcrumbs li a {
        font-size: 10px;
    }
}


