/* DrukCyr */
@font-face {
    font-family: "DrukCyr";
    font-style: normal;
    font-display: swap;
    font-weight: 500;
    src: url("../fonts/DrukCyr-Medium.woff2") format("woff2"), url("../fonts/DrukCyr-Medium.woff") format("woff");
}
@font-face {
    font-family: "DrukCyr";
    font-style: normal;
    font-display: swap;
    font-weight: 700;
    src: url("../fonts/DrukCyr-Bold.woff2") format("woff2"), url("../fonts/DrukCyr-Bold.woff") format("woff");
}

