
// about
.about {
    padding-bottom: 0;

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 60px;
        margin-bottom: 109px;
    }

    &__title {
        max-width: 1060px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;

        & .orange {
            color: var(--text-orange);
        }

        & .underline {
            position: relative;
            &::before {
                content: '';
                display: block;
                width: 100%;
                height: 8px;
                background: url('./../img/icons/about-underline.svg') no-repeat center / cover;
                position: absolute;
                right: -14px;
                bottom: -21px;
            }
        }

        &-photos {
            display: inline-flex;
        }

        &-photo {
            flex-shrink: 0;
            border-radius: 100%;
            border: 5px solid var(--stroke-white);
            background: var(--background-on-black-light-black);
            overflow: hidden;
            width: 72px;
            height: 72px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 28px;
            line-height: 114%;
            text-transform: uppercase;
            color: var(--text-primary-white);

            &:not(:first-child) {
                margin-left: -15px;
            }
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }

    &__text {
        max-width: 440px;
    }

    &__cards {
        display: flex;
        align-items: flex-end;
        min-height: 650px;
    }

    &__card {
        padding: 64px 40px;
        display: flex;
        flex-direction: column;
        gap: 32px;

        &-num {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 28px;
            line-height: 114%;
            text-transform: uppercase;
            color: var(--text-primary-black);
            text-align: center;
            & span {
                font-size: 120px;
                line-height: 103%;
                color: var(--text-primary-white);
            }
        }
        &-text {
            line-height: 122%;
            text-align: center;
            color: var(--text-secondary-orange);
        }

        &.white {
            & .about__card-num {
                color: var(--text-primary-black);
                & span {
                    color: var(--text-orange);
                }
            }
            & .about__card-text {
                color: var(--text-secondary-black);
            }
        }

        &:nth-child(1) {
            min-height: 350px;
            border-radius: 10px 10px 0 0;
            background: var(--background-base-orange) url('./../img/about/card-1.svg') no-repeat bottom center / 100% 100%;
            transition: all 0.5s ease;
        }
        &:nth-child(2) {
            min-height: 560px;
            border-radius: 20px;
            background: var(--background-on-white-light-gray) url('./../img/about/card-2.svg') no-repeat top center / 100% auto;
            //transform: rotate(-7deg) translate(5px, -11px);
            justify-content: flex-end;
            transition: all 0.5s ease 0.4s;
        }
        &:nth-child(3) {
            min-height: 440px;
            border-radius: 60px;
            background: radial-gradient(#E47362, var(--background-base-orange));
            //transform: rotate(5deg) translate(5px, -14px);
            justify-content: center;
            transition: all 0.5s ease 0.8s;
        }
        &:nth-child(4) {
            padding: 80px 40px;
            border-radius: 20px;
            background: var(--background-on-white-light-gray) url('./../img/about/card-4.svg') no-repeat bottom center / 100% auto;
            min-height: 624px;
            transition: all 0.5s ease 1.2s;
        }


        &.animation-active {
            &:nth-child(1) {
                min-height: 650px;
            }
            &:nth-child(2) {
                border-radius: 120px;
                transform: rotate(-7deg) translate(5px, -11px);
            }
            &:nth-child(3) {
                border-radius: 60px;
                transform: rotate(5deg) translate(5px, -14px);
            }
            &:nth-child(4) {
                border-radius: 240px;
            }
        }
    }
}


@media (max-width: 1330px) {
    .about {
        &__top {
            flex-direction: column;
            align-items: stretch;
            justify-content: space-between;
            gap: 60px;
            margin-bottom: 138px;
        }
        &__text {
            max-width: 650px;
        }

        &__cards {
            display: flex;
            flex-wrap: wrap;
            min-height: auto;
        }

        &__card {
            flex: 1 0 40%;
            &:nth-child(1) {
                border-radius: 10px;
                min-height: 350px;
            }
            &:nth-child(2) {
                min-height: 540px;
            }
            &:nth-child(3) {
                min-height: 440px;
                transition: all 0.5s ease 0.1s;
            }
            &:nth-child(4) {
                padding: 60px 40px;
                transition: all 0.5s ease 0.5s;
            }
            &.animation-active {
                &:nth-child(1) {
                    min-height: 600px;
                }
            }
        }
    }
}

@media (max-width: 1024px) {
    .about {
        padding-bottom: 40px;
        &__cards {
            flex-wrap: wrap;
            gap: 12px;
        }

        &__card {
            padding: 28px 24px;
            gap: 16px;

            &:nth-child(1) {
                min-height: 320px;
                border-radius: 10px;
                background: var(--background-base-orange) url('./../img/about/card-1-mob.svg') no-repeat bottom center / 100% 100%;
            }
            &:nth-child(2) {
                min-height: 320px;
                border-radius: 10px;
                background: var(--background-on-white-light-gray) url('./../img/about/card-2.svg') no-repeat top -70px center / 205px 170px;
            }
            &:nth-child(3) {
                min-height: auto;
                border-radius: 20px;
            }
            &:nth-child(4) {
                padding: 40px;
                border-radius: 20px;
                background: var(--background-on-white-light-gray) url('./../img/about/card-4-mob.svg') no-repeat bottom center / 100% 125px;
                min-height: 380px;
                transition: all 0.5s ease 1.2s;
            }


            &.animation-active {
                &:nth-child(1) {
                    min-height: 380px;
                }
                &:nth-child(2) {
                    border-radius: 40px;
                    transform: rotate(4deg);
                }
                &:nth-child(3) {
                    border-radius: 20px;
                    transform: rotate(-4deg);
                }
                &:nth-child(4) {
                    border-radius: 120px;
                }
            }
        }
    }
}

@media (max-width: 768px) {
    .about {
        padding-bottom: 40px;
        &__top {
            gap: 32px;
            margin-bottom: 24px;
        }

        &__title {
            max-width: 1060px;
            display: flex;
            flex-wrap: wrap;
            align-items: center;

            & .orange {
                color: var(--text-orange);
            }

            & .underline {
                &::before {
                    height: 5px;
                    right: 0px;
                    bottom: -12px;
                }
            }

            &-photo {
                border: 3px solid var(--stroke-white);
                width: 42px;
                height: 42px;
                font-size: 18px;

                &:not(:first-child) {
                    margin-left: -8px;
                }
            }
        }

        &__cards {
            flex-direction: column;
            gap: 12px;
        }

        &__card {
            padding: 28px 24px;
            gap: 16px;

            &-num {
                font-size: 24px;
                & span {
                    font-size: 56px;
                }
            }

            &:nth-child(1) {
                min-height: 200px;
            }
            &:nth-child(2) {
                min-height: 240px;
                border-radius: 10px;
            }
            &:nth-child(3) {
                min-height: auto;
                border-radius: 20px;
            }
            &:nth-child(4) {
                padding: 40px;
                border-radius: 20px;
                min-height: 310px;
                transition: all 0.5s ease 0.8s;
            }


            &.animation-active {
                &:nth-child(1) {
                    min-height: 240px;
                }
                &:nth-child(2) {
                    border-radius: 40px;
                    transform: none;
                }
                &:nth-child(3) {
                    border-radius: 20px;
                    transform: none;
                }
                &:nth-child(4) {
                    border-radius: 120px;
                }
            }
        }
    }
}