
// work
.work {

    &__row {
        display: flex;
        gap: 10px;
    }

    &__content {
        border-radius: 30px;
        width: 100%;
        max-width: calc(50% - 5px);
        padding: 65px 35px;
        background: var(--background-base-orange) url('./../img/work/work-bg.svg') no-repeat center / cover;
        display: flex;
        flex-direction: column;
        overflow: hidden;

        transform: translate(50%, 0%);
        transition: all 0.6s ease;

        &.animation-active {
            transform: none;
        }
    }

    &__top {
        margin-bottom: 57px;
        & .section__top-title::before {
            background: #fff;
        }

        & .counter-blocks {
            & span {
                &:first-child {
                    border-color: var(--stroke-orange);
                    background: var(--background-base-light-orange);
                    color: var(--text-primary-white);
                }
            }
        }
    }

    &__main {
        flex-grow: 1;
        margin-bottom: 65px;
        position: relative;
        padding-bottom: 100px;
        &::after {
            content: "";
            display: block;
            width: calc(100% + 72px);
            height: 4px;
            border-bottom: 4px dotted var(--background-base-light-orange);
            position: absolute;
            bottom: 0;
            left: -36px;
        }
    }

    &__title {
        font-size: 100px;
        margin-bottom: 52px;
        & span {
            display: inline-block;
            color: var(--text-primary-black);
            padding: 0 14px;
            background-image: url("data:image/svg+xml,%3Csvg width='475' height='117' viewBox='0 0 475 117' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M471.571 0.805288H465.391H456.461L452.277 2.13262L433.914 0.805288H418.553L412.239 1.61891L403.192 0.805288H384.08L343.234 0L336.493 0.805288L330.12 2.66578L328.996 0.805288H294.149L274.803 2.13262L269.044 0.805288H255.93H235.297H219.579L213.212 2.48806L197.552 0.805288L177.241 2.13262L174.239 0.805288H167.123L160.75 1.77719L143.142 0.805288H132.275L111.249 2.13262L108.294 0.805288L95.1744 1.95491H67.4484L59.4898 0.805288H55.0834H32.2261L8.99428 2.48806L3.19511 0.805288V3.73209L7.49621 6.22293L3.19511 11.2018V14.4008L0 17.8274L3.19511 26.1413V29.3375L0 30.9397L3.19511 37.196V44.4519L5.61777 47.1205L3.19511 52.0994V56.0092L0 67.211L3.19511 67.9219V69.7018V77.3465V81.6145L6.37266 81.97L3.19511 85.5271V92.8164L0 105.085L3.19511 106.914V114.826H12.7395H37.3465L41.9694 113.604L46.645 114.826H85.3023L87.684 117L97.0528 114.826H142.018L145.512 113.265L163.746 114.826L177.615 113.604L212.837 114.826H255.181L259.447 113.621L277.29 114.826H316.152L343.234 113.087L348.858 114.826H374.337H406.563H412.327H447.034L453.775 113.265L463.893 114.826H475L471.571 110.955V102.063V99.9279L469.517 91.3946L471.571 88.3706V83.9249L475 79.8318L471.571 76.2802V72.9008V69.1687L469.142 61.4657L471.571 59.3886L475 48.8976L471.571 42.8524V37.196L468.393 35.9186L471.571 30.0483V27.5603L475 25.9025L471.571 23.7282L468.013 13.6899L471.571 12.8013L475 3.37666L471.571 0.805288Z' fill='white'/%3E%3C/svg%3E");
            background-repeat: no-repeat;
            background-size: 100% 100%;
        }
    }

    &__text {
        font-size: 20px;
        line-height: 1.3;
        color: var(--base-orange-0);
        max-width: 600px;
    }

    &__btn {
        width: fit-content;
    }

    &__picture {
        border-radius: 30px;
        width: 100%;
        max-width: calc(50% - 5px);
        background: var(--background-on-black-light-black);
        overflow: hidden;

        transform: translate(-50%, 0%);
        transition: all 0.6s ease;

        &.animation-active {
            transform: none;
        }

        &-pic {
            display: block;
            width: 100%;
            height: 100%;
            & img {
                width: 100%;
                height: 100%;
                
                object-fit: cover;
            }
        }
    }
}

@media (max-width: 1300px) {
    .work {

        &__row {
            flex-direction: column;
            gap: 10px;
        }
        &__content {
            transform: none;
        }
        &__content, &__picture {
            max-width: 100%;
        }
        &__title {
            font-size: 88px;
        }
        &__picture {
            transform: translate(0%, -100%);
            &-pic {
                & img {
                    max-height: 900px;
                    object-position: top;
                }
            }
        }
    }
}

@media (max-width: 768px) {
    .work {

        &__row {
            display: flex;
            gap: 10px;
        }
    
        &__content {
            border-radius: 20px;
            padding: 40px 10px;
        }
    
        &__top {
            margin-bottom: 28px;
        }
    
        &__main {
            margin-bottom: 32px;
            padding-bottom: 32px;
        }
    
        &__title {
            font-size: 44px;
            margin-bottom: 24px;
        }
    
        &__text {
            font-size: 14px;
        }
    
        &__btn {
            width: 100%;
        }
    
        &__picture {
            &-pic {
                & img {
                    max-height: 450px;
                }
            }
        }
    }
}
@media (max-width: 500px) {
    .work {
        &__picture {
            &-pic {
                & img {
                    max-height: 350px;
                }
            }
        }
    }
}