// roots
:root {
	--container-width: 1860px;
	--container-padding: 20px;

	--font-family: "DrukCyr", sans-serif;
	--second-family: "Onest", sans-serif;


	--base-orange-0: #fff;
	--base-orange-1: #fff0ee;
	--base-orange-2: #ffcec7;
	--base-orange-3: #ffada1;
	--base-orange-4: #ff8b7b;
	--base-orange-5: #ff6a54;
	--base-orange-6: #de523e;
	--base-orange-7: #bc3d2b;
	--base-black-0: #fff;
	--base-black-1: #f5f5f7;
	--base-black-2: #e8e9f1;
	--base-black-3: #cfd1dc;
	--base-black-4: #aeb1c1;
	--base-black-5: #9397a7;
	--base-black-6: #797d8e;
	--base-black-7: #616475;
	--base-black-8: #494c5b;
	--base-black-9: #2c2e37;
	--background-base-black: var(--base-black-10);
	--background-on-white-light-gray: var(--base-black-1);
	--background-base-white: var(--base-black-0);
	--background-on-black-light-black: var(--base-black-9);
	--base-black-10: #1c1e28;
	--background-base-orange: var(--base-orange-6);
	--text-primary-black: var(--base-black-10);
	--text-orange: var(--base-orange-6);
	--text-primary-white: var(--base-black-0);
	--text-secondary-black: var(--base-black-7);
	--stroke-black: var(--base-black-10);
	--stroke-orange: var(--base-orange-6);
	--stroke-white: var(--base-black-0);
	--text-secondary-white: var(--base-black-4);
	--stroke-gray: var(--base-black-2);
	--background-on-white-gray: var(--base-black-2);
	--background-on-black-gray: var(--base-black-8);
	--stroke-dark-gray: var(--base-black-3);
	--stroke-light-black: var(--base-black-8);
	--text-secondary-orange: var(--base-orange-1);
	--stroke-light-orange: var(--base-orange-5);
	--text-tertiary-white: var(--base-black-8);
	--text-tertiary-black: var(--base-black-3);
	--background-base-light-orange: var(--base-orange-5);
	--state-orange: var(--base-orange-6);
	--state-orange-hover: var(--base-orange-5);
	--state-gray-disabled: var(--base-black-2);
	--state-gray-disabled-dark: var(--base-black-4);
	--state-white: var(--base-orange-0);
	--stroke-ligh-light-black: var(--base-black-9);
	--state-black: var(--base-black-10);
	--state-black-disabled: var(--base-black-6);
	--state-black-disabled-white: var(--base-black-8);
	--color: #fff;
	--base-error-error: #f04438;
}