
// platforms
.platforms {

    &__title {
        max-width: 1275px;
        margin-bottom: 32px;
    }

    &__cards {
        display: flex;
        list-style-type: none;
        position: relative;
        height: calc(46vw);
        max-height: 822px;
        overflow: hidden;
    }

    &__card {
        position: absolute;

        &:nth-child(1) {top: 0; right: 25%; z-index: 3; max-width: 42%;}
        &:nth-child(2) {top: -55px; right: -20px; z-index: 4; max-width: 33%;}
        &:nth-child(3) {top: 33%; left: 18%; z-index: 2; max-width: 40%;}
        &:nth-child(4) {top: -25px; left: -10px; max-width: 34%;}
    }

    &__card-pic {
        display: block;
        width: 100%;
        height: 100%;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__card-text {
        position: absolute;
        top: 323px;
        left: 332px;
        transform: rotateX(-2deg) rotateY(26deg) rotateZ(4deg);
    }

    &__card-name {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 45px;
        line-height: 1.04545;
        text-transform: uppercase;
        color: var(--text-primary-white);
        margin-bottom: 16px;
    }
}

@media (max-width: 1200px) {
    .platforms {
        &__card {
            &:nth-child(2) {top: -25px;}
        }
    }
}
@media (max-width: 800px) {
    .platforms {
        overflow: hidden;

        &__title {
            max-width: 1275px;
            margin-bottom: 24px;
        }
    
        &__cards {
            flex-direction: column;
            height: 100%;
            max-height: none;
            overflow: visible;
        }
    
        &__card {
            position: static;
    
            &:nth-child(1) {margin-left: -17%; z-index: 1; max-width: 100%;}
            &:nth-child(2) {margin-top: -50%; margin-right: -25%; margin-left: auto; transform: rotate(-5deg); z-index: 2; max-width: 90%;}
            &:nth-child(3) {margin-top: -60%; margin-left: -15%; transform: rotate(5deg); z-index: 3; max-width: 95%;}
            &:nth-child(4) {margin-top: -53%; margin-right: -8%; margin-left: auto; transform: rotate(5deg); z-index: 4; max-width: 93%;}
        }
    
        &__card-pic {
            display: block;
            width: 100%;
            height: 100%;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    
        &__card-text {
            position: absolute;
            top: 323px;
            left: 332px;
            transform: rotateX(-2deg) rotateY(26deg) rotateZ(4deg);
        }
    
        &__card-name {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 45px;
            line-height: 1.04545;
            text-transform: uppercase;
            color: var(--text-primary-white);
            margin-bottom: 16px;
        }
    }
}