
// gallery
.gallery {
    overflow: hidden;

    &__title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
    }

    &__swiper {
        overflow: visible;
    }

    &__wrap {
        //overflow: visible;

        // position: relative;
        // &::after {
        //     content: '';
        //     display: block;
        //     width: 182px;
        //     height: 100%;
        //     background: linear-gradient(90deg, rgba(29, 31, 40, 0) 0%, #1d1f28 100%);
        //     position: absolute;
        //     top: 0;
        //     right: -4px;
        //     pointer-events: none;
        //     z-index: 3;
        // }
    }


    &__swiper-slide {
        &:nth-child(2n) {
            & img {
                height: 700px;
            }
        }
    }

    &__card {
        display: block;
        border-radius: 20px;
        overflow: hidden;
        & img {
            width: 100%;
            height: 100%;
            height: 480px;
            object-fit: cover;
        }
    }
}

@media (max-width: 1200px) {
    .gallery {
        &__swiper-slide:nth-child(2n) {
            & img {
                height: 540px;
            }
        }

        &__card {
            border-radius: 10px;
            & img {
                height: 320px;
            }
        }
    }
}
@media (max-width: 768px) {
    .gallery {
        &__swiper-btns {
            display: none;
        }

        &__swiper-slide:nth-child(2n) {
            & img {
                height: 340px;
            }
        }

        &__card {
            border-radius: 10px;
            & img {
                height: 260px;
            }
        }
    }
}
