
// stages
.stages {
    padding-bottom: 0;
    overflow: hidden;

    &__wrap {
        position: relative;
        &::before {
            content: '';
            display: block;
            width: 100vw;
            height: 4px;
            border-top: 4px dotted var(--background-on-white-gray);
            transform: translate(-50%, 0%);
            position: absolute;
            top: 0;
            left: 50%;
        }
    }

    &__swiper-slide {
        height: auto;
        border-right: 4px dotted var(--background-on-white-gray);
        &:first-child {
            border-left: 4px dotted var(--background-on-white-gray);
        }
        &:nth-child(2n) .stages__card-sticker {transform: rotate(7deg);}
    }

    &__card {
        padding: 40px;
        padding-bottom: 0;
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    &__card-sticker {
        width: 104px;
        height: 64px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        margin-bottom: 60px;
        transform: rotate(-7deg);

        & img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }

        & span {
            position: relative;
            z-index: 2;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 28px;
            line-height: 1.14286;
            text-transform: uppercase;
            color: var(--base-orange-0);
        }
    }

    &__card-text {
        flex-grow: 1;
        margin-bottom: 65px;
        font-size: 18px;
        line-height: 1.22222;
        color: var(--text-secondary-black);
    }
    
    &__card-title {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 40px;
        line-height: 1.1;
        text-transform: uppercase;
        color: var(--text-primary-black);
        margin-bottom: 24px;
    }

    &__card-pic {
        display: block;
        width: fit-content;
        margin: 0 auto;
    }
}

@media (max-width: 1300px) {
    .stages {
        &__swiper {
            overflow: visible;
        }
    }
}

@media (max-width: 768px) {
    .stages {
        padding-bottom: 0;

        &__card {
            padding: 22px 20px 0;
        }
    
        &__card-sticker {
            width: 64px;
            height: 40px;
            margin-bottom: 24px;
            & span {
                font-size: 17px;
            }
        }
    
        &__card-text {
            margin-bottom: 47px;
            font-size: 14px;
        }
        
        &__card-title {
            font-size: 28px;
            margin-bottom: 16px;
        }
    
        &__card-pic {
            & img {
                width: 100%;
                max-height: 160px;
                object-fit: contain;
            }
        }
    }
}