
// projects
.projects {
    overflow: hidden;

    &__title {
        margin-bottom: 120px;

        & span {
            position: relative;
            padding-right: 152px;
            &::after {
                content: '';
                display: inline-block;
                width: 40px;
                height: 49px;
                background-image: url("data:image/svg+xml,%3Csvg width='40' height='49' viewBox='0 0 40 49' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M10.2084 1.47155C10.6138 0.576357 11.5173 0 12.5184 0H32.4824C34.3594 0 35.5832 1.93754 34.7423 3.58568L29.0637 14.7155H37.4677C39.7202 14.7155 40.8464 17.3839 39.2547 18.9438L9.33996 48.2595C7.45293 50.1087 4.30954 48.2055 5.15546 45.726L11.5323 26.9784H2.52763C2.10653 26.9784 1.69212 26.8753 1.32202 26.6785C0.951918 26.4816 0.637861 26.1973 0.408371 25.8513C0.178881 25.5053 0.0412297 25.1086 0.00792093 24.6972C-0.0253878 24.2859 0.0467011 23.8729 0.217639 23.4957L10.2084 1.47155Z' fill='%23DE523E'/%3E%3C/svg%3E");
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
                position: absolute;
                top: 50%;
                right: 56px;
                transform: translate(0%, -50%);
            }
        }
    }


    &__cards {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }

    &__card {
        border-radius: 30px;
        padding: 12px;
        padding-top: 68px;
        min-height: 800px;
        position: relative;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        transition: all 0.3s ease;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            z-index: 2;
            width: 100%;
            height: 100%;
            backdrop-filter: blur(10px);
            background: rgba(0, 0, 0, 0.3);
            opacity: 0;
            transition: all 0.3s ease;
        }
        
        &-pic {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            height: 100%;
            transition: all 0.9s ease;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                transition: all 0.9s ease;
            }
        }
    
        &-link {
            position: absolute;
            top: 12px;
            right: 12px;
            border-radius: 50px;
            background: var(--background-base-white);
            width: 52px;
            height: 52px;
            display: flex;
            align-items: center;
            justify-content: center;
            z-index: 3;
            &::after {
                content: '';
                display: inline-block;
                width: 18px;
                height: 18px;
                background-image: url("data:image/svg+xml,%3Csvg width='18' height='18' viewBox='0 0 18 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11.3647 12.7808L8.53629 15.6093C7.55812 16.5874 6.37961 17.0763 5.00075 17.0758C3.6219 17.0753 2.44338 16.5865 1.46522 15.6093C0.487057 14.632 -0.00178938 13.4535 -0.00131776 12.0737C-0.000846155 10.6939 0.488 9.51542 1.46522 8.5382L4.29365 5.70977L5.70786 7.12398L2.87943 9.95241C2.29018 10.5417 1.99555 11.2488 1.99555 12.0737C1.99555 12.8987 2.29018 13.6058 2.87943 14.195C3.46869 14.7843 4.1758 15.0789 5.00075 15.0789C5.82571 15.0789 6.53282 14.7843 7.12207 14.195L9.9505 11.3666L11.3647 12.7808ZM6.41497 12.0737L5.00075 10.6595L10.6576 5.00266L12.0718 6.41688L6.41497 12.0737ZM12.7789 11.3666L11.3647 9.95241L14.1931 7.12398C14.7824 6.53473 15.077 5.82762 15.077 5.00266C15.077 4.1777 14.7824 3.4706 14.1931 2.88134C13.6039 2.29209 12.8968 1.99746 12.0718 1.99746C11.2469 1.99746 10.5398 2.29209 9.9505 2.88134L7.12208 5.70977L5.70786 4.29556L8.53629 1.46713C9.51445 0.488965 10.6932 -0.000115645 12.0725 -0.000116467C13.4519 -0.000115961 14.6301 0.488966 15.6074 1.46713C16.5846 2.44529 17.0737 3.62404 17.0746 5.00337C17.0755 6.3827 16.5865 7.56097 15.6074 8.5382L12.7789 11.3666Z' fill='%231C1E28'/%3E%3C/svg%3E");
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
            }
        }
    
        &-title {
            border-radius: 18px;
            padding: 40px;
            backdrop-filter: blur(6px);
            background: rgba(29, 31, 40, 0.5);
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 56px;
            line-height: 1.14286;
            text-transform: uppercase;
            color: var(--text-primary-white);
            position: relative;
            z-index: 3;
        }
    
        &-year {
            opacity: 0.2;
        }

        &-btn-more {
            display: none;
        }

        &-inner {
            display: contents;
        }
    
        &-wrap {
            position: relative;
            z-index: 3;
            opacity: 0;
            max-height: 0;
            transition: all 0.6s ease;
        }

        &-content {
            border-radius: 18px;
            padding: 40px;
            backdrop-filter: blur(6px);
            background: rgba(29, 31, 40, 0.5);
            margin-top: 4px;
        }

        &-infos {
            display: flex;
            flex-direction: column;
            gap: 32px;
            margin-bottom: 32px;
        }

        &-info {
            display: flex;
            gap: 16px;
        }

        &-chapter {
            font-weight: 500;
            font-size: 14px;
            line-height: 1.42857;
            text-transform: uppercase;
            color: var(--text-tertiary-black);
            flex: 1 0 20%;
        }

        &-text {
            flex: 1 0 70%;
            font-size: 18px;
            line-height: 1.22222;
            color: var(--text-primary-white);
        }

        &-statistic {
            display: flex;
            gap: 10px;
            & li {
                flex: 1 0 15%;
                border-radius: 10px;
                padding: 22px 12px;
                text-align: center;
                position: relative;
                background: url('./../img/projects/statistic-card.png') no-repeat center / 100% 100%;
                
                &:first-child {
                    background: url('./../img/projects/statistic-card-first.png') no-repeat center / 100% 100%;
                }
                &:last-child {
                    background: url('./../img/projects/statistic-card-last.png') no-repeat center / 100% 100%;
                }

                &:not(:last-child) {
                    &::before {
                        content: '';
                        position: absolute;
                        top: 50%;
                        left: 100%;
                        transform: translate(0%, -50%);
                        display: inline-block;
                        width: 10px;
                        height: 16px;
                        background-image: url("data:image/svg+xml,%3Csvg width='10' height='16' viewBox='0 0 10 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.28571 3.84C6.76143 1.55299 10 0 10 0C10 0 7.92844 2.94502 6.78571 4.48C5.64299 6.01498 5 7.04 5 7.04L9.64286 8C9.64286 8 6.63911 11.3923 5 12.8C2.92423 14.5827 0 16 0 16C0 16 2.33004 12.7432 3.21429 11.52C3.92857 10.5319 4.64286 9.6 4.64286 9.6L0 8.64C0 8.64 1.71912 6.21097 4.28571 3.84Z' fill='white'/%3E%3C/svg%3E");
                        background-repeat: no-repeat;
                        background-position: center;
                        background-size: contain;
                    }
                }

                & span {
                    display: block;
                    font-size: 14px;
                    line-height: 1.42857;
                    color: var(--text-secondary-white);
                    margin-bottom: 8px;
                }
                & p {
                    font-family: var(--font-family);
                    font-weight: 500;
                    font-size: 40px;
                    line-height: 1.1;
                    text-transform: uppercase;
                    color: var(--text-orange);
                }
            }
        }
    }
}

@media (hover: hover) { 
    .projects { 
        &__card {    
            &:hover {
                &::before {
                    opacity: 1;
                }
                & .projects__card-wrap {
                    max-height: 100%;
                    opacity: 1;
                }
                & .projects__card-pic {
                    //transform: translate(-50%, -50%) scale(1.2);
                    
                }
            }
        }
    }
}

