
// vacances
.vacances {

    &__top {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        gap: 24px;
        margin-bottom: 60px;
    }

    &__title {
        max-width: 605px;
    }

    &__link {
        display: flex;
        align-items: center;
        gap: 20px;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 28px;
        line-height: 114%;
        text-transform: uppercase;
        color: var(--text-primary-white);

        & span {
            display: block;
            max-width: 200px;
        }

        &-icon {
            flex-shrink: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 80px;
            height: 80px;
            border-radius: 20px;
            background: var(--background-base-white);
            transition: all 0.3s ease;
            & svg path {
                transition: all 0.3s ease;
            }
        }

        &:hover {
            text-decoration: underline;
            & .vacances__link-icon {
                background: #ff0002;
                & svg path {
                    &:nth-child(2) {fill: #FF0002;}
                    &:nth-child(1) {fill: #fff;}
                }
            }
        }
    }

    &__list {
        list-style-type: none;
        counter-reset: item;
    }

    &__item {
        padding: 64px 0;
        padding-left: 180px;
        position: relative;

        &:first-child {
            &::before {
                content: '';
                width: 100vw;
                height: 4px;
                border-bottom: 4px dotted var(--background-on-black-gray);
                position: absolute;
                top: 0;
                left: 50%;
                transform: translate(-50%, 0);
            }
        }
        &::after {
            content: '';
            width: 100vw;
            height: 4px;
            border-bottom: 4px dotted var(--background-on-black-gray);
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, 0);
        }
        
        &-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 30px;
            position: static;
            
            &::before {
                content: counter(item, decimal-leading-zero);
                counter-increment: item;
                display: block;
                font-family: var(--font-family);
                font-weight: 500;
                font-size: 150px;
                line-height: 80%;
                color: var(--background-on-black-light-black);
                position: absolute;
                top: 64px;
                left: 0;
            }

            &.accordion--active .accordion__btn {
                transform: rotate(90deg);
            }
        }

        &-name {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 56px;
            line-height: 114%;
            text-transform: uppercase;
            color: var(--text-primary-white);
            margin-bottom: 28px;
        }

        &-tags {
            display: flex;
            flex-wrap: wrap;
            gap: 8px;
        }

        &-tag {
            border-radius: 10px;
            padding: 16px 24px;
            background: var(--background-on-black-light-black);
            font-size: 18px;
            line-height: 122%;
            color: var(--text-primary-white);
        }

        &-btn {
            flex-shrink: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 10px;
            background: var(--background-on-black-light-black);
            width: 64px;
            height: 64px;

            & .accordion__btn {
                transition: all 0.3s ease;
            }
        }

        &-content {
            display: flex;
            align-items: flex-end;
            justify-content: space-between;
            gap: 50px;
            margin-top: 84px;
        }

        &-text {
            font-size: 18px;
            line-height: 122%;
            color: var(--state-white);

            & ul {
                display: flex;
                flex-direction: column;
                gap: 16px;
                & li {
                    position: relative;
                    padding-left: 20px;
                    &::before {
                        content: '';
                        display: block;
                        width: 12px;
                        height: 12px;
                        position: absolute;
                        top: 4px;
                        left: 0;
                        background: url("./../img/icons/star.svg") no-repeat center / contain;
                    }
                }
            }
        }

        &-block:not(:last-child) {
            margin-bottom: 48px;
        }

        &-caption {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 28px;
            line-height: 114%;
            text-transform: uppercase;
            color: var(--text-primary-white);
            margin-bottom: 24px;
        }
    }
}

@media (max-width: 1300px) {
    .vacances {
        &__item {
            padding: 50px 0;
            padding-left: 90px;
            
            &-top {
                &::before {
                    font-size: 100px;
                }
            }

            &-name {
                font-size: 44px;
            }
        }
    }
}
@media (max-width: 900px) {
    .vacances {
        &__top {
            flex-direction: column;
            align-items: stretch;
            gap: 24px;
        }
        &__item {
            &-content {
                flex-direction: column;
                align-items: stretch;
            }
        }
    }
}
@media (max-width: 768px) {
    .vacances {
        &__top {
            margin-bottom: 32px;
        }

        &__link {
            gap: 12px;
            font-size: 18px;
            & span {
                max-width: 130px;
            }
            &-icon {
                border-radius: 10px;
                width: 52px;
                height: 52px;
                & svg  {
                    max-width: 30px;
                }
            }
        }

        &__list {
            list-style-type: none;
            counter-reset: item;
        }

        &__item {
            padding: 30px 0;
            
            &-top {
                gap: 10px;
                align-items: flex-start;
                
                &::before {
                    font-size: 32px;
                    position: static;
                }
            }

            &-title {
                flex-grow: 1;
            }

            &-name {
                font-size: 28px;
                margin-bottom: 12px;
            }

            &-tags {
                gap: 4px;
            }

            &-tag {
                border-radius: 10px;
                padding: 12px;
                font-size: 12px;
            }

            &-btn {
                border-radius: 8px;
                width: 28px;
                height: 28px;
                & svg {
                    max-width: 6px;
                }
            }

            &-content {
                gap: 32px;
                margin-top: 40px;
            }

            &-text {
                font-size: 14px;
            }

            &-block:not(:last-child) {
                margin-bottom: 32px;
            }

            &-caption {
                font-size: 24px;
                margin-bottom: 20px;
            }
        }
    }
}

.photo__card {
    border-radius: 30px;
    display: block;
    width: 100%;
    height: 100%;
    max-height: 1000px;
    overflow: hidden;
    & img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        min-height: 440px;
    }
}
@media (max-width: 768px) {
    .photo__card {
        border-radius: 20px;
        max-height: 600px;
        & img {
            min-height: 240px;
        }
    }
}