
// footer
.footer {

    &__wrap {
        padding-top: 100px;
        border-radius: 30px;
        background: var(--background-on-black-light-black);
        margin: 0 10px 10px 10px;
        position: relative;
        overflow: hidden;
    }

    &__row {
        display: flex;
        gap: 40px;
        margin-bottom: 190px;
    }

    &__left {
        flex: 1 0 40%;
    }

    &__nav {
        display: flex;
        gap: 80px;
        margin-bottom: 80px;
    }

    &__nav-list {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &__nav-title, &__nav-link {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 28px;
        line-height: 1.14286;
        text-transform: uppercase;
        color: var(--text-primary-white);
    }

    &__nav-title {
        color: var(--state-black-disabled-white);
    }

    &__nav-link {
        transition: all 0.3s ease;
        &:hover {
            color: var(--stroke-orange);
        }
    }


    &__policy {
        display: flex;
        flex-direction: column;
        gap: 10px;
        & > * {
            font-weight: 500;
            font-size: 14px;
            line-height: 1.42857;
            text-transform: uppercase;
            color: var(--text-secondary-white);
        }
        & a {
            text-decoration: underline;
            transition: all 0.3s ease;
            &:hover {
                color: var(--base-orange-0);
            }
        }
    }

    &__right {
        flex: 1 0 40%;
    }

    &__contacts {
        display: flex;
        flex-direction: column;
        gap: 30px;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 40px;
        line-height: 1.1;
        text-transform: uppercase;
        color: var(--base-orange-0);
        margin-bottom: 64px;
        
        & a {
            color: var(--base-orange-0);
            text-decoration: underline;
            transition: all 0.3s ease;
            &:hover {
                color: var(--stroke-orange);
            }
        }
    }

    &__networks {
        display: flex;
        gap: 36px;
    }

    &__images {
        display: flex;
        align-items: flex-end;
        justify-content: space-around;
        gap: 25px;
    }

    &__lights {
        position: absolute;
        top: -5px;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        pointer-events: none;

        & img {
            max-width: 20%;
            height: 100%;
            opacity: 0;
            transition: opacity 1.8s ease;

            &:nth-child(2n) {
                transition: opacity .6s ease;
            }
            &:nth-child(3n) {
                transition: opacity 1.8s ease;
            }
            &:nth-child(5n) {
                transition: opacity 1.2s ease;
            }

            &.animation-active {
                opacity: 1;
            }
        }
    }
}

.scroll-top {
    position: absolute;
    top: 100px;
    right: 37px;
    width: 64px;
    height: 64px;
    border-radius: 10px;
    border: 1px solid var(--stroke-light-black);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
    & svg path {
        transition: all 0.3s ease;
    }

    &:hover {
        background: var(--background-base-white);
        & svg path {
            stroke: var(--stroke-orange);
        }
    }
}

@media (max-width: 1300px) {
    .footer {
        &__lights {
            & img {
                max-width: 33%;
                &:nth-child(1), &:nth-child(2) {
                    display: none;
                }
            }
        }
    }
}

@media (max-width: 1024px) {
    .footer {
        &__row {
            flex-direction: column;
            gap: 50px;
            margin-bottom: 120px;
        }
    
        &__left {
            display: contents;
        }
    
        &__nav {
            margin-bottom: 24px;
        }
    
        &__policy {
            order: 3;
        }

        &__lights {
            & img {
                width: 100%;
                max-width: 100%;
                &:nth-child(3), &:nth-child(4) {
                    display: none;
                }
            }
        }
    }
}

@media (max-width: 768px) {
    .footer {

        &__wrap {
            padding-top: 64px;
            border-radius: 20px 20px 0 0;
            margin: 0;
        }
    
        &__row {
            gap: 40px;
            margin-bottom: 64px;
        }
    
        &__nav {
            flex-direction: column-reverse;
            gap: 40px;
        }
    
        &__nav-list {
            display: flex;
            flex-direction: column;
            gap: 10px;
        }
    
        &__nav-title, &__nav-link {
            font-size: 24px;
        }
    
    
        &__policy {
            & > * {
                font-size: 10px;
            }
        }

        &__contacts {
            gap: 14px;
            font-size: 28px;
            margin-bottom: 32px;
        }

        &__btns {
            display: flex;
            align-items: flex-end;
            justify-content: space-between;
        }
    
        &__networks {
            flex-direction: column;
            gap: 16px;
        }
    
        &__images {
            gap: 10px;
        }
    }
    .scroll-top {
        position: static;
        width: 44px;
        height: 44px;
        & svg {
            max-width: 12px;
        }
    }
}