
// reviews
.reviews {
    &__top {
        display: flex;
        align-items: end;
        justify-content: space-between;
        gap: 60px;
        margin-bottom: 60px;
    }

    &__title {
        max-width: 860px;
        display: flex;
        gap: 0 12px;
        flex-wrap: wrap;
        align-items: center;

        &-icons {
            display: inline-flex;
        }

        &-icon {
            flex-shrink: 0;
            border-radius: 100%;
            background: var(--background-base-orange);
            overflow: hidden;
            width: 72px;
            height: 72px;

            &:not(:first-child) {
                margin-left: -15px;
            }
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }


    &__swiper-slide {
        height: auto;
    }

    &__card {
        height: 100%;
        display: flex;
        flex-direction: column;
        border-radius: 30px;
        padding: 12px;
        background: var(--background-on-white-light-gray);
    }

    &__card-main {
        flex-grow: 1;
        padding: 32px 32px 24px 32px;
    }

    &__card-text {
        color: var(--text-primary-black);
        max-height: 157px;
        transition: all 0.3s ease;

        display: -webkit-box;
        -webkit-line-clamp: 6;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;

        &.active {
            -webkit-line-clamp: none;
        }
    }

    &__card-btn {
        border-radius: 10px;
        padding: 12px;
        background: var(--background-base-white);
        color: var(--text-orange);
        margin-top: 24px;
        display: flex;
        align-items: center;
        gap: 8px;
        &::after {
            border-color: var(--text-orange);
        }
    }

    &__card-autor {
        border-radius: 18px;
        padding: 32px;
        background: var(--background-base-white);
        display: flex;
        align-items: center;
        gap: 24px;
    }

    &__card-photo {
        display: block;
        flex-shrink: 0;
        & img {
            width: 90px;
            height: 90px;
            object-fit: cover;
            border-radius: 50%;
        }
    }

    &__card-name {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 40px;
        line-height: 1.1;
        text-transform: uppercase;
        color: var(--text-primary-black);
        margin-bottom: 16px;
    }

    &__card-post {
        font-size: 18px;
        line-height: 1.22222;
        color: var(--text-secondary-black);
    }
}

@media (max-width: 1300px) {
    .reviews { 
        overflow: hidden;

        &__title {
            &-icon {
                width: 60px;
                height: 60px;
            }
        }

        &__swiper {
            overflow: visible;
        }

        &__card-main {
            flex-grow: 1;
            padding: 22px;
        }
    
        &__card-autor {
            padding: 22px;
        }
    
        &__card-photo {
            & img {
                width: 72px;
                height: 72px;
            }
        }

        &__card-name {
            font-size: 32px;
            margin-bottom: 6px;
        }
    }
}
@media (max-width: 768px) {
    .reviews {
        &__top {
            margin-bottom: 32px;

            & .swiper-btns {
                display: none;
            }
        }
    
        &__title {   
            &-icon {
                width: 36px;
                height: 36px;
    
                &:not(:first-child) {
                    margin-left: -15px;
                }
            }
        }
    

    
        &__card {
            border-radius: 20px;
            padding: 8px;
        }
    
        &__card-main {
            padding: 16px;
        }
    
        &__card-text {
            max-height: 182px;
            -webkit-line-clamp: 10;
            &.active {
                -webkit-line-clamp: none;
            }
        }
    
        &__card-btn {
            border-radius: 8px;
            padding: 10px;
            font-size: 12px;
            margin-top: 12px;
        }
    
        &__card-autor {
            border-radius: 12px;
            padding: 16px;
            gap: 12px;
            align-items: flex-start;
        }
    
        &__card-photo {
            & img {
                width: 58px;
                height: 58px;
            }
        }
    
        &__card-name {
            font-size: 24px;
            margin-bottom: 6px;
        }
    
        &__card-post {
            font-size: 12px;
        }
    }
}